<template>
	<!-- Register Modal -->
	<!--div class="modal fade modalStyle" id="Register" tabindex="-1" role="dialog" aria-hidden="true"-->
	<div v-if="layerView" class="layerPopup">
		<div class="modal-dialog">
			<div class="modal-content">
				<div class="modal-body">
					<div>
						<h2> </h2>
						<hr>
						<div class="of_h">
							<h5 class="inline-block" style="width:20%"> ID *</h5>
							<div style="width:80%" class="of_h inline-block float-right">
								<input type="text" v-model="loginId" @change="fnChangeUserId" class="inputStyle" style="width:70%" title=" ID ">
								<a @click="fnCheckDupcUser()" class="btnStyle1 backLightGray float-right" style="width:28%" title=""></a>
							</div>
						</div>
						<div class="of_h consolMarginTop">
							<h5 class="inline-block" style="width:20%"> *</h5>
							<input type="text" v-model="userName" class="inputStyle float-right" style="width:80%" title="  ">
						</div>
						<div class="of_h consolMarginTop">
							<h5 class="inline-block" style="width:20%"> *</h5>
							<input type="text" v-model="hpNumber" class="inputStyle float-right" style="width:80%" title="  ">
						</div>
						<div class="of_h consolMarginTop">
							<h5 class="inline-block" style="width:20%"></h5>
							<select v-model="roleCd" class="selectStyle2 float-right" style="width:80%" title=" ">
								<option value="USER">USER</option>
								<option value="OWNER">OWNER</option>
								<option value="ADMIN">ADMIN</option>
							</select>
						</div>
						<p class="color3 consolMarginTop"><i class="far fa-info-circle"></i>   SMS       .</p>
					</div>
					<div class="text-center mt20">
						<a @click="fnRegisterUser()" class="btnStyle1 backBlack mr5" title=""></a>
						<a @click="fnCloseLayer()" class="btnStyle1 backWhite" title=""></a>
					</div>
				</div>
			</div>
		</div>
	<!--/div-->
	</div>
</template>

<script>
import userApi from '../service/userApi'
import tokenSvc from '@/common/token-service';
import confirm from "@/modules/commonUtil/service/confirm";

export default {
	name: 'RegisterLayer',
	props: {
		layerView: {
			type: Boolean,
			require: true,
			default: false
		},
		title: {
			type: String,
			require: false
		}
	},
	data() {
		return {
			isDupcUser			: false,
			loginId		: '',
			userName	: '',
			hpNumber	: '',
			roleCd		: 'USER'
		}
	},
	methods: {
		// 
		fnCloseLayer(){
			this.$emit('update:layerView', false);
		},
		//
		fnCheckDupcUser() {
			console.log(">>> start fnCheckDupcUser.");

			// ID 
			if(this.loginId == "" || this.loginId == null) {
				confirm.fnAlert("", "ID .");
				return false;
			}

			// ID() 
			if(!this.fnIsEmail(this.loginId)) return false;

			var params = {
				"loginId" :this.loginId
			}

			userApi.checkDupcUser(params).then(response =>{
				var result = response.data;
				if(result.success) {
					confirm.fnAlert("", "  ID .");
					this.isDupcUser =  true;
				} else {
					confirm.fnAlert("", "   ID .");
					this.isDupcUser = false;
				}
			});
			console.log(">>> end fnCheckDupcUser.");
		},
		// 
		fnRegisterUser() {
			console.log("fnRegisterUser Start");
			
			// 
			if(!this.fnIsDupcChk()) return false;

			//   
			if(!this.fnInputCheckReq()) return false;

			//   
			if(!this.fnIsEmail(this.loginId)) return false;

			var params = {
				"loginId"	: this.loginId,
				"userName"	: this.userName,
				"hpNumber"	: this.hpNumber,
				"roleCd"	: this.roleCd,
				"regId"		: tokenSvc.getToken().principal.loginId,
				"corpId"	: tokenSvc.getToken().principal.corpId
			}

			userApi.registerUser(params).then(response =>{
				var result = response.data;
				if(result.success) {
					confirm.fnAlert("", "  .");
					this.$parent.fnSelectUserList();
				} else {
					confirm.fnAlert("", result.message);
				}
			});
			this.fnCloseLayer();
		},
		// 
		fnIsDupcChk() {
			if(!this.isDupcUser) {
				confirm.fnAlert("", "ID  .");
				return false;
			}
			return true;
		},
		//  
		fnIsEmail(asValue) {
			var regExp = /^[0-9a-zA-Z]([-_\.]?[0-9a-zA-Z])*@[0-9a-zA-Z]([-_\.]?[0-9a-zA-Z])*\.[a-zA-Z]{2,3}$/i;
			if(!regExp.test(asValue)) {
				confirm.fnAlert("", "    .");
				return false;
			}
			return true;
		},
		//   
		fnInputCheckReq() {
			var loginId = this.loginId;
			var userName = this.userName;
			var hpNumber = this.hpNumber;

			if(loginId == "" || loginId == null) {
				confirm.fnAlert("", "ID .");
				return false;
			}
			
			if(userName == "" || userName == null) {
			confirm.fnAlert("", " .");
			return false;
			}

			if(hpNumber == "" || hpNumber == null) {
				confirm.fnAlert("", " .");
				return false;
			}

			return true;
		},
		// ID 
		fnChangeUserId() {
			this.isDupcUser = false;
		}
	}
}
</script>