<template>
  <div class="modal fade modalStyle" id="preRegExPop" tabindex="-1" role="dialog" aria-hidden="true">
		<div class="modal-dialog">
			<div class="modal-content">
				<div class="modal-body">
					<div class="of_h">
						<h5 class="lc-1">     </h5>
						<hr>
						<div class="mt20">
							<h4 class="font-normal inline-block width90"> *</h4>
							<select v-model="reqType" name="admin030502_1" class="selectStyle3 width414 font-size14" >
								<option value="spam">  </option>
                <option value="system">    </option>
                <option value="government"></option>
                <option value="special"></option>
							</select>
						</div>
            <div class="mt15">
							<h4 class="font-normal inline-block width90">1</h4>
              <input id="file1" ref="fileRef1" type="file" class="btnStyle7 minWidthAuto float" style="display : inline; width : 79%;">
						</div>
            <div class="mt15">
							<h4 class="font-normal inline-block width90">2</h4>
              <input id="file2" ref="fileRef2" type="file" class="btnStyle7 minWidthAuto float" style="display : inline; width : 79%;">
						</div>
            <div class="mt15">
							<h4 class="font-normal inline-block width90">3</h4>
              <input id="file3" ref="fileRef3" type="file" class="btnStyle7 minWidthAuto float" style="display : inline; width : 79%;">
						</div>
            <div class="mt15">
							<h4 class="font-normal inline-block width90">4</h4>
              <input id="file4" ref="fileRef4" type="file" class="btnStyle7 minWidthAuto float" style="display : inline; width : 79%;">
						</div>
					</div>
					<div class="text-center mt40">
            <a @click="fnSaveWithFile" class="btnStyle3 black font14" data-toggle="modal"></a>
						<a @click="fnCloseLayer" ref="closeBtn" class="btnStyle3 white font14" data-dismiss="modal"></a>						
					</div>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
import axios from 'axios'
import tokenSvc from '@/common/token-service';

export default {
  name: 'preRegExPop',
  data() {
    return {
      fileName1 : "",
      filePath1 : "",
      fileName2 : "",
      filePath2 : "",
      fileName3 : "",
      filePath3 : "",
      fileName4 : "",
      filePath4 : "",
    }
  },
  props: {
    reqType : {
      type: String,
      require: true
    }
  },
  methods: {
    // 
    fnCloseLayer: function() {
      $("#preRegExPop").modal("hide");
    },
    //    
    async fnSaveWithFile(){
      if(this.$refs.fileRef1.files[0].value == 0){
        alert(" .");
        return;
      }

      var fd = new FormData();

      //  
      fd.append('uploadFiles', this.$refs.fileRef1.files[0]);
      fd.append('uploadFiles', this.$refs.fileRef2.files[0]);
      fd.append('uploadFiles', this.$refs.fileRef3.files[0]);
      fd.append('uploadFiles', this.$refs.fileRef4.files[0]);
      
      fd.append('loginId', tokenSvc.getToken().principal.userId);
      fd.append('corpId', tokenSvc.getToken().principal.corpId);
      fd.append('reqType', this.reqType);

      await axios.post('/projectApi/manage/savePreRegExWithUploadFiles',
        fd, {
          headers: {
            'Content-Type': 'multipart/form-data'
          }
        }
      ).then( response => {
        var result = response.data;
        if(result.success) {
          alert(" .");
          //  
          this.$refs.closeBtn.click();
          //   
          this.$parent.fnMoveMainTab('callbackManage');
        } else {
          alert(" .");
        }
      })
      .catch(function (e) {
        console.log(e);
        alert(" .");
      });
    }
  }
}
</script>
