<template>
	<div>
	<provisionViewService :svcFlag.sync="svcFlag" />
	<provisionViewPri :priFlag.sync="priFlag" />
	<section class="userSection width540 mt70">
		<h2 class="text-center"></h2>

		<input type="checkbox" id="agree0" class="checkStyle3 mainCheck" value=" " v-model="agree0" @click="checkAll($event.target.checked)"><label for="agree0">  .</label>	

		<hr>

		<div class="of_h">
			<input type="checkbox" id="agree1" class="checkStyle3 subCheck" value="  " v-model="agree1"><label for="agree1"><span class="color1">[]</span>   </label>
			<a href="#self" class="float-right provisionMore" data-toggle="modal" @click.privent="svcPopView"> <i class="far fa-chevron-right"></i></a>
		</div>
		<div class="of_h mt10">
			<input type="checkbox" id="agree2" class="checkStyle3 subCheck" value="  " v-model="agree2"><label for="agree2"><span class="color1">[]</span>   </label>
			<a href="#self" class="float-right provisionMore" data-toggle="modal" @click.privent="priPopView"> <i class="far fa-chevron-right"></i></a>
		</div>
		<div class="of_h mt10">
			<input type="checkbox" id="agree3" class="checkStyle3 subCheck" value="   " v-model="agree3"><label for="agree3">[]    </label>
		</div>

		<hr>

		<div class="of_h">
			<h4 class="font-normal float-left font-size14 mt15" style="width:20%"> *</h4>
			<div class="float-left" style="width:80%">
				<input type="text" id="email" class="inputStyle" placeholder=" " v-model="email">
				<p class="font-size12 color3 mt10">   <span class="color1">1</span>.</p>
			</div>
		</div>

		<button type="submit" class="btn btn-login mt45" @click.prevent="emailSend"> </button>
		<button type="submit" class="btn btn-login mt45" @click.prevent="apiTest">API TEST</button>
	</section>

	</div>
</template>

<script>
import confirm from "@/modules/commonUtil/service/confirm";
import signUpApi from "@/modules/signUp/service/api"
import provisionViewService from "./provisionViewService.vue";
import provisionViewPri from "./provisionViewPri.vue";

export default {
  components: {
    //modal
	provisionViewService,
	provisionViewPri
  },
  data() {
    return {
		//    
		agree0 : false,
		agree1 : false,
		agree2 : false,
		agree3 : false,
		email : "",
		randonNum : "",
		//    flag
		priFlag : false,
		svcFlag : false
    }
  },

  methods: {
	  emailSend() {
		//   validation  
		let termsVali = this.termsValidation();
		if(!termsVali) return false;

		//    
		let emailVali = this.emailValidation();
		if(!emailVali) return false;

		//   
		let isEmail = this.isEmail(this.email);
		if(!isEmail) {
			confirm.fnAlert("", "    .");
			return false;
		}
		//    

		//      CM_USER  INSERT ->   
		this.insertEmailUser();
	  },

	  //    validation
	  termsValidation() {
		  if(!this.agree1){
			  confirm.fnAlert("", "   .");
			  return false;
		  } else if(!this.agree2) {
			  confirm.fnAlert("", "   .");
			  return false;
		  } else {
			  return true;
		  }
	  },

	  // email validation (email     .)
	  emailValidation() {
		  var email = this.email;
			if(email == "" || email == null){
				confirm.fnAlert("", "   email  .");
				return false;
			} else {
				return true;
			}
	  },
	  isEmail(asValue) {
		var regExp = /^[0-9a-zA-Z]([-_\.]?[0-9a-zA-Z])*@[0-9a-zA-Z]([-_\.]?[0-9a-zA-Z])*\.[a-zA-Z]{2,3}$/i;

		return regExp.test(asValue); //    true 	
	  },

	  //    
	  checkAll(check) {
		  if(check) {
			  this.agree1 = true;
			  this.agree2 = true;
			  this.agree3 = true;
		  } else { 
			  this.agree1 = false;
			  this.agree2 = false;
			  this.agree3 = false;
		  }
	  },

	  //  INSERT ->  
	  insertEmailUser() {
		  var vm = this;
		  var randomNum = this.randomNum;
		  var params = {
			  "email" : this.email,
			  "randomNum" : this.randomNum
		  }
		  /*
		  if(randomNum == "") {
			  confirm.fnAlert("", "  .     .");
			  return false;
		  }*/


		  signUpApi.insertEmailUser(params).then(function(response) { 
			  var result = response.data.success;		  
			  if(result) {
				vm.$router.push({name : "mailSend"});
			  } else {
				var message = response.data.message;
				confirm.fnAlert("", message);
				vm.$router.push({name : "login"});
			  }
		  });
	  },

	  //   
	  svcPopView() {
		  this.svcFlag = true;
		  $("#provisionView1").modal('show');
	  },
	  svcPopViewClose() {
		  this.svcFlag = false;
		  $("#provisionView1").modal('hide');
	  },
	  priPopView () {
		  this.priFlag = true;
		  $("#provisionView2").modal('show');
	  },
	  priPopViewClose() {
		  this.priFlag = false;
		  $("#provisionView2").modal('hide');
	  },

	  apiTest() {
		  signUpApi.apiTest().then(function(res) {

		  });
	  }
  }
}
</script>