<template>
  <div>
    <modal
      :save_status.sync="save_status"
      :row_data="row_data"
    >
    </modal>
    
		<article>
      <div class="contentHeader">
        <h2> >  </h2>
      </div>

      <!--  -->
      <div class="row">
        <div class="col-xs-12">
          <div class="menuBox">
            <div class="mt20">
              <div class="of_h">
                <h4 class="font-normal inline-block" style="width:10%"> </h4>
                <input id="srcProjectName" type="text" class="inputStyle inline-block" style="width:30%" v-model="srcProjectName">
                <h4 class="font-normal inline-block ml40" style="width:10%"></h4>
                <select id="srcUseYn" class="selectStyle2 inline-block" style="width:30%" v-model="srcUseYn">
                  <option value=""></option>
                  <option value="Y">Y</option>
                  <option value="N">N</option>
                </select>
                <a @click="fnSearch" class="btnStyle2 float-right" title=""></a>
              </div>	
            </div>
          </div>
        </div>
      </div>
      <!--  -->

      <!--  -->
      <div class="row mt20">
        <div class="col-xs-12">
          <div class="of_h">
            <div class="float-right">
              <a @click="fnProjectReg" class="btnStyle3 gray font13 minWidth120"></a>
            </div>
          </div>
          <table cellspacing="0" id="list" class="table_skin1 tbl-striped" style="width:100%">
            <thead>
              <th>No.</th>
              <th></th>
              <th>ID</th>
              <th> </th>
              <th></th>
              <th></th>
              <th></th>
              <th></th>
              <th></th>
              <th></th>
            </thead>
            <tbody>
              <tr v-for="(data, index) in items" :key="index">
                <td>
                  {{ index + 1 }}
                </td>
                <td>
                  {{ data.projectName }}
                </td>
                <td>
                  {{ data.projectId }}
                </td>
                <td>
                  {{ data.useCh }}
                </td>
                <td>
                  {{ data.regDt }}
                </td>
                <td>
                  {{ data.payType }}
                </td>
                <td>
                  {{ data.projectMemberCnt }}
                </td>
                <td>
                  {{ data.regDt }}
                </td>
                <td>
                  <button @click="fnProjectDetail(data)"><a></a></button>
                  <button @click="fnProjectUpdate(data)"><a></a></button>
                  <button @click="fnProjectDeleteConfirm(data)"><a></a></button>
                </td>
                <td>
                  <button ><a></a></button>
                </td>
              </tr>
            </tbody>
          </table>
        </div>
      </div>
      <!--  -->
    </article>
  </div>
</template>


<script>
import projectApi from '../service/projectApi'
import tokenSvc from '@/common/token-service';

import {eventBus} from "@/modules/commonUtil/service/eventBus";
import confirm from "@/modules/commonUtil/service/confirm"

import modal from "./bp-project-manage-detail.vue";

export default {
  components: {
    modal
  },
  data() {
    return {
      save_status : '', //   
      row_data : {},
      //  
      srcProjectName : "",
      srcUseYn : "",
      // 
      deleteProjectId : "",
      // 
      items : []
    }
  },
  mounted() {
    this.fnSearch();
  },
  methods: {
    // 
    fnSearch() {
      var vm = this;
      var params = {
        "srcProjectName"  : this.srcProjectName,
        "srcUseYn"        : this.srcUseYn,
        "loginId"         : tokenSvc.getToken().principal.userId,
        "roleCd"         : tokenSvc.getToken().principal.roleCd
      }
       
      projectApi.selectProjectList(params).then(response =>{
        vm.items = response.data.data;
      });
    },
    // 
    fnProjectReg : function(){
        this.save_status = 'C';
        this.row_data = {};
        jQuery("#projectPop").modal("show");
      },
    // 
    fnProjectDetail(data) {
      this.$router.push( {name:"projectMain",params:{"projectId" : data.projectId, "projectName" : data.projectName}} );
    },
    // 
    fnProjectUpdate(data) {
      jQuery("#projectPop").modal("show");
      this.save_status = 'U';
      this.row_data = data;
    },
    // 
    fnProjectDeleteConfirm(data){
      this.deleteProjectId = data.projectId;
      eventBus.$on('callbackEventBus', this.fnProjectDelete);
      confirm.fnConfirm("", "    .  ?", "");
    },
    // 
    fnProjectDelete(){
      var params = {
        "projectId"   : this.deleteProjectId,
        "sts"         : "D",
        "userDto"     : tokenSvc.getToken().principal    
      };

      projectApi.saveProject(params).then(response =>{
        var result = response.data;

        if(result.success) {
          confirm.fnAlert("", "");
          //  
          this.fnSearch();
        } else {
          var msg = response.data.message;
          confirm.fnAlert("", msg);
        }
      }); 
    }
  }
}
</script>