<template>
  <!-- content -->
  <div class="row row-no-margin">
    <div class="contentHeader">
      <h2>  </h2>
      <!-- <a href="#self" class="btnStyle1 absolute top0 right0" onClick="window.location.reload()" title="  / "> <i class="fal fa-book-open"></i></a> -->
    </div>

    <ImageManagePopUp @img-callback="fnCallbackImgInfo" :imgMngOpen.sync="imgMngOpen" :useCh="useCh" ref="imgMng"></ImageManagePopUp>
    <ImageUploadPopUp :imgUploadOpen.sync="imgUploadOpen"></ImageUploadPopUp>

    <!--  -->
    <div class="row">
      <div class="phone3 inline-block" style="width:30%">
        <div class="phoneFixed">
          <!-- phoneWrap -->
          <div class="phoneWrap">
            <img src="@/assets/images/common/phoneMockup1.svg" alt=" ">
            <div class="phoneTextWrap">
              <div class="phoneText2">
                <p v-if="fnIsEmpty(tmpltData.tmpltTitle)"> </p>
                <p v-else>{{tmpltData.tmpltTitle}}</p>
              </div>
              <div v-if="tmpltData.msgType == 'IMAGE' && fnIsEmpty(tmpltData.imgUrl)" class="phoneText2 mt10 text-center" style="padding:65px">
                <i class="fas fa-image-polaroid" style="font-size:38px; color:#D5D5D5"></i>
                <p class="font-size14 color3 mt15"> </p>
              </div>
              <div v-if="tmpltData.msgType == 'IMAGE' && !fnIsEmpty(tmpltData.imgUrl)" class="phoneText2 mt10 text-center"
                :style="'padding:65px;background-repeat: no-repeat;background-size: cover;background-image: url('+tmpltData.imgUrl+');'">
              </div>
              <div class="scroll-y">
                <p v-if="fnIsEmpty(tmpltData.tmpltContent) && (tmpltData.msgKind != 'A' || fnIsEmpty(tmpltData.rcvblcNumber))" class="font-size14 color4 mt10"> </p>
                <p v-else class="font-size14 color4 mt10">
                  <span v-html="$gfnCommonUtils.newLineToBr(tmpltData.tmpltContent)"></span>
                  <br v-if="!fnIsEmpty(tmpltData.tmpltContent)"/>
                  <span v-if="tmpltData.msgKind == 'A' && !fnIsEmpty(tmpltData.rcvblcNumber)">
                    {{tmpltData.rcvblcNumber}}
                  </span>
                </p>
              </div>
            </div>
          </div>
          <!-- //phoneWrap -->
        </div>
      </div>
      <div class="of_h inline-block vertical-top consoleCon" style="width:60%">
        <div class="of_h user-phone">
          <div class="float-left" style="width:28%">
            <h4> *</h4>
          </div>
          <div class="float-left" style="width:72%">
            <input type="text" class="inputStyle float-right" title=" " v-model="tmpltData.tmpltName" maxlength="100">
          </div>
        </div>
        <div class="of_h user-phone">
          <div class="float-left" style="width:28%">
            <h4> *</h4>
          </div>
          <div class="float-left" style="width:72%">
            <input type="radio" id="msgType_BASE" value="BASE" v-model="tmpltData.msgType">
            <label for="msgType_BASE" class="mr30"></label>
            <input type="radio" id="msgType_IMAGE" value="IMAGE" v-model="tmpltData.msgType">
            <label for="msgType_IMAGE"></label>
          </div>
        </div>
        <div class="of_h user-phone">
          <div class="float-left" style="width:28%">
            <h4> *</h4>
          </div>
          <div class="float-left" style="width:72%">
            <input type="radio" id="msgKind_A" value="A" v-model="tmpltData.msgKind">
            <label for="msgKind_A" class="mr30"></label>
            <input type="radio" id="msgKind_I" value="I" v-model="tmpltData.msgKind">
            <label for="msgKind_I"></label>
          </div>
        </div>
        <div class="of_h user-phone">
          <div class="float-left" style="width:28%">
            <h4>    *</h4>
          </div>
          <div class="float-left" style="width:72%">
            <input type="radio" id="otherProjectUseYn_Y" value="Y" v-model="tmpltData.otherProjectUseYn">
            <label for="otherProjectUseYn_Y" class="mr30"></label>
            <input type="radio" id="otherProjectUseYn_N" value="N" v-model="tmpltData.otherProjectUseYn">
            <label for="otherProjectUseYn_N"></label>
          </div>
        </div>
        <div class="of_h user-phone">
          <div class="float-left" style="width:28%">
            <h4></h4>
          </div>
          <div class="float-left" style="width:72%">
            <input type="text" class="inputStyle float-right" title=" " id="tmpltTitle" name="tmpltTitle" v-model="tmpltData.tmpltTitle" maxlength="45">
          </div>
        </div>
        <div class="of_h user-phone">
          <div class="float-left" style="width:28%">
            <h4> *</h4>
          </div>
          <div class="float-left" style="width:72%">
            <textarea class="textareaStyle height120" placeholder="" v-model="tmpltData.tmpltContent" maxlength="2000"></textarea>
            <div v-if="tmpltData.msgKind == 'A'">
              <p class="color5">  ,  () ,  ()   .</p>
              <input type="text" id="rcvblcNumber" name="rcvblcNumber" class="inputStyle float-right mt10" title=" " v-model="tmpltData.rcvblcNumber" placeholder=" >    " maxlength="45">
              <p class="color5">   .   (: ) .</p>
            </div>
          </div>
        </div>
        <div class="of_h user-phone">
            <div class="float-left" style="width:28%">
              <h4>(EXT)</h4>
            </div>
            <div class="float-left" style="width:72%">
              <input type="text" class="inputStyle float-right" title="(EXT) " v-model="tmpltData.adtnInfo" maxlength="45">
            </div>
          </div>
        <div v-if="tmpltData.msgType == 'IMAGE'" class="of_h user-phone">
          <div class="float-left" style="width:28%">
            <h4></h4>
          </div>
          <div class="float-left" style="width:72%">
            <div class="of_h">
              <div class="float-left" style="width:25%">
                <a @click="fnOpenImageManagePopUp" class="btnStyle1 backLightGray width100_" title=""></a>
              </div>
              <ul class="float-right attachList" style="width:74%; padding:5px 15px; height:30px;">
                <li><a @click="fnDelImg">{{fnSubString(tmpltData.imgUrl, 0, 35)}}  <i v-if="!fnIsEmpty(tmpltData.imgUrl)" class="fal fa-times"></i></a></li>
              </ul>
            </div>
          </div>
        </div>
        <div class="mt30 float-right">
          <a @click="fnSavePushTemplate" class="btnStyle2" title="">{{isInsert ? '' : ''}}</a>
          <router-link :to="{ name: 'pushTemplateList' }" tag="a" class="btnStyle2 backRed ml10"></router-link>
        </div>
      </div>
    </div>
  </div>
  <!-- //content -->
</template>

<script>
import templateApi from "@/modules/template/service/templateApi.js";
import ImageManagePopUp from "@/modules/commonUtil/components/bp-imageManage.vue";
import ImageUploadPopUp from "@/modules/commonUtil/components/bp-imageUpload.vue";
import confirm from "@/modules/commonUtil/service/confirm.js";
import {eventBus} from "@/modules/commonUtil/service/eventBus";

export default {
  name: 'pushTemplateManage',
  components : {
    ImageManagePopUp,
    ImageUploadPopUp
  },
  props: {
    tmpltId: {
      type: String,
      require: false,
      default: function() {
        return '';
      }
    },
    componentsTitle: {
      type: String,
      require: false,
      default: function() {
        return '  ';
      }
    },
  },
  data() {
    return {
      imgMngOpen : false,
      imgUploadOpen : false,
      useCh : 'PUSH',
      isInsert : true,
      tmpltData : {imgUrl:''}
    }
  },
  mounted() {
    this.fnSetTemplateInfo();
  },
  methods: {
    fnDelImg(){
      this.tmpltData.imgUrl = '';
      this.tmpltData.fileId = '';
    },
    fnSubString(str, sIdx, length){
      var shortStr = ''
      if(!this.fnIsEmpty(str)){
        shortStr = str.toString();
        if(shortStr.length > length){
          shortStr = shortStr.substring(sIdx, length) + '...  ';
        } else {
          shortStr = shortStr + '  ';
        }
      }
      return shortStr;
    },
    //
    fnIsEmpty(str){
      if(str) return false;
      else return true
    },
    //   
    fnSelectPushTmpltInfo(){
      var vm = this;
      var params = {tmpltId: this.tmpltId};
      templateApi.selectPushTmpltInfo(params).then(response => {
        var result = response.data;
        if(result.success) {
          result.data.forEach(function(obj){
            vm.tmpltData = obj;
            vm.tmpltData.otherProjectUseYn = (obj.projectId == 'ALL' ? 'Y' : 'N');
          });
        } else {
          confirm.fnAlert(this.componentsTitle, result.message);
          this.tmpltData = {};
          this.isInsert = true;
        }
      });
    },
    //template  
    fnSetTemplateInfo(){
      if(!this.$gfnCommonUtils.isEmpty(this.tmpltId)){
        this.isInsert = false;
        this.fnSelectPushTmpltInfo();
      } else {
        this.isInsert = true;
      }
    },
    fnOpenImageManagePopUp(){
      this.$refs.imgMng.fnSearch();
      this.imgMngOpen = !this.imgMngOpen;
    },
    fnOpenImageUploadPopUp : function(){
      this.imgUploadOpen = !this.imgUploadOpen;
    },
    fnCallbackImgInfo(imgInfo) {
      this.tmpltData.imgUrl = imgInfo.chImgUrl;
      this.tmpltData.fileId = imgInfo.fileId;
    },
    // 
    fnIsValid(){
      if(!this.tmpltData.tmpltName){
        confirm.fnAlert(this.componentsTitle, ' .');
        return false;
      }
      if(!this.tmpltData.msgType){
        confirm.fnAlert(this.componentsTitle, ' .');
        return false;
      }
      if(!this.tmpltData.msgKind){
        confirm.fnAlert(this.componentsTitle, ' .');
        return false;
      }
      if(!this.tmpltData.otherProjectUseYn){
        confirm.fnAlert(this.componentsTitle, '   .');
        return false;
      }
      if(!this.tmpltData.tmpltTitle){
        confirm.fnAlert(this.componentsTitle, ' .');
        return false;
      }
      if(!this.tmpltData.tmpltContent){
        confirm.fnAlert(this.componentsTitle, ' .');
        return false;
      }
      if(this.tmpltData.msgKind == 'A' && !this.tmpltData.rcvblcNumber){
        confirm.fnAlert(this.componentsTitle, '   .');
        return false;
      }
      if(this.tmpltData.msgType == 'IMAGE' && !this.tmpltData.imgUrl){
        confirm.fnAlert(this.componentsTitle, ' .');
        return false;
      }
      if(this.tmpltData.msgType == 'IMAGE' && !this.tmpltData.fileId){
        confirm.fnAlert(this.componentsTitle, '  .   .');
        return false;
      }
      return true;
    },
    //
    fnSavePushTemplate(){
      // 
      if(this.fnIsValid() == false) return;

      var saveType = (this.isInsert ? '' : '');
      eventBus.$on('callbackEventBus', this.fnProcSavePushTemplate);
      confirm.fnConfirm(this.componentsTitle, "  "+saveType+"?", "");
    },
    async fnProcSavePushTemplate(){
      //DATA Set
      var params = this.tmpltData;
      if(this.tmpltData.msgType != 'IMAGE') {
        params.imgUrl = '';
        params.fileId = '';
      }
      if(this.tmpltData.msgKind != 'A') {
        params.rcvblcNumber = '';
      }

      //
      await templateApi.savePushTmplt(params).then(response => {
        var result = response.data;
        if(result.success) {
          eventBus.$on('callbackEventBus', this.fnSaveAlertCallback);
          confirm.fnAlert(this.componentsTitle, '.', 'CALLBACK');
        } else {
          confirm.fnAlert(this.componentsTitle, result.message);
        }
      });
    },
    fnSaveAlertCallback(){
      if(this.isInsert){
        this.$router.push('pushTemplateList')
      }
    }

  }
}
</script>