				<div class="float-left" style="width:78%">
					<input type="text" class="inputStyle" placeholder=" " v-model="passwordChk">
				</div>
			</div>

			<div class="of_h mt20">
				<div class="float-left" style="width:22%"><h4 class="font-normal font-size14 mt3">SMS *</h4></div>
				<div class="float-left" style="width:78%">
					<input type="radio" name="payment" value="Y" class="cBox" id="sms_agree01" v-model="smsCertifyYn"> <label for="sms_agree01" class="font-normal mr30"></label>
					<input type="radio" name="payment" value="N" class="cBox" id="sms_agree02" v-model="smsCertifyYn"> <label for="sms_agree02" class="font-normal"></label>
				</div>
			</div>
		</div>

		<h3 class="font-size18 mt40">  *</h3>
		<div class="joinBox mt10">
			<div class="of_h">
				<div class="float-left" style="width:22%"><a href="#self" class="btnStyle7 vertical-middle" style="min-width:auto; width:100%"> </a></div>
				<div class="float-right" style="width:75%">
					<input type="text" class="inputStyle" placeholder="" v-model="phoneCerti">
				</div>
			</div>
		</div>

		<h3 class="font-size18 mt40"> </h3>
		<div class="joinBox mt10">
			<div class="of_h">
				<div class="float-left" style="width:22%"><h4 class="font-normal font-size14 mt15">*</h4></div>
				<div class="float-left" style="width:78%">
					<input type="text" class="inputStyle" placeholder=" ( -   )" v-model="regno">
				</div>
			</div>

			<div class="of_h mt10">
				<div class="float-left" style="width:22%"><h4 class="font-normal font-size14 mt15">*</h4></div>
				<div class="float-left" style="width:78%">
					<input type="text" class="inputStyle" placeholder="" v-model="corpNm">
				</div>
			</div>

			<div class="of_h mt10">
				<div class="float-left" style="width:22%"><h4 class="font-normal font-size14 mt15">*</h4></div>
				<div class="float-left" style="width:78%">
					<input type="text" class="inputStyle" placeholder="" v-model="ceoNm">
				</div>
			</div>

			<div class="of_h mt10">
				<div class="float-left" style="width:22%"><h4 class="font-normal font-size14 mt15">*</h4></div>
				<div class="float-left" style="width:78%">
					<div class="float-left" style="width:30%"><input type="text" class="inputStyle" placeholder="" v-model="busiType"></div>
					<div class="float-right" style="width:50%">
						<div class="float-left" style="width:35%"><h4 class="font-normal font-size14 mt15">*</h4></div>
						<div class="float-left" style="width:65%">
							<input type="text" class="inputStyle" placeholder="" v-model="busiClass">
						</div>
					</div>
				</div>
			</div>

			<div class="of_h mt10">
				<div class="float-left" style="width:22%"><h4 class="font-normal font-size14 mt15">*</h4></div>
				<div class="float-left" style="width:78%">
					<div class="float-left" style="width:75%"><input type="text" class="inputStyle" placeholder="" v-model="woplaceAddress"></div>
					<div class="float-right" style="width:22%"><a href="#self" class="btnStyle7 vertical-middle" style="min-width:auto; width:100%"> </a></div>					
				</div>
				<div class="float-right mt5" style="width:78%"><input type="text" class="inputStyle" placeholder=" " v-model="woplaceAddressDetail"></div>
			</div>

			<div class="of_h mt10">
				<div class="float-left" style="width:22%"><h4 class="font-normal font-size14 mt15"></h4></div>
				<div class="float-left" style="width:78%">
					<input type="text" class="inputStyle" placeholder=" ( -   )" v-model="wireTel">
				</div>
			</div>

			<div class="of_h mt10">
				<div class="float-left" style="width:22%"><h4 class="font-normal font-size14 mt15">*</h4></div>
				<div class="float-left" style="width:78%">
					<div class="float-left" style="width:75%"><input type="text" class="inputStyle" placeholder="" v-model="attachFileSeq"></div>
					<div class="float-right" style="width:22%"><a href="#self" class="btnStyle7 vertical-middle" style="min-width:auto; width:100%"></a></div>					
				</div>
				<div class="float-right font-size12 color3 mt5" style="width:78%">* PDF, JPG, JPEG, PNG  . (: 5MB)</div>
			</div>
		</div>

		<h3 class="font-size18 mt40"> </h3>
		<div class="joinBox mt10">
			<div class="of_h mt10">
				<div class="float-left" style="width:22%"><h4 class="font-normal font-size14 mt15"> *</h4></div>
				<div class="float-left" style="width:78%">
					<div class="float-left" style="width:75%"><input type="text" class="inputStyle" placeholder=",  3  30 " v-model="domainName" :disabled="this.domainNameChk==true" maxlength="30"></div>
					<div class="float-right" style="width:22%"><a href="#s elf" class="btnStyle7 vertical-middle" style="min-width:auto; width:100%" @click.prevent="domainNameChkBtn"></a></div>					
				</div>
			</div>
		</div>
		<div class="text-center mt70"><a href="#" class="btn btn-login" @click.prevent="signUp"></a></div>
	</section>
	</div>
</template>

<script>
import confirm from "@/modules/commonUtil/service/confirm";
import signUpApi from "@/modules/signUp/service/api"

export default {
  components: {
    //modal
  },
  data() {
    return {
		loginId : "",				// 
		password : "",				// 
		passwordChk : "",			//  
		smsCertifyYn : "Y",			// sms 
		phoneCerti : "01050959083",			//     (   )
		phoneCertiChk : true,		//    TF (  defalte false )
		regno : "",					// 
		corpNm : "",				// 
		ceoNm : "",					// 
		busiType : "",				// 
		busiClass : "",				// 
		woplaceAddress : "",		// 
		woplaceAddressDetail : "",	// 
		wireTel : "",				//  
		attachFileSeq : "",			// 
		domainName : "",			//  
		domainNameChk : "N",		//   TF
    }
  },
  //   (),    
  //      
  watch: {
	  domainName : function() {
		  return this.domainName = this.domainName.replace(/[^a-z0-9]/g, '');
	  },
	  phoneCerti : function() {
		  return this.phoneCerti = this.phoneCerti.replace(/[^0-9]/g, '');
	  },
	  regno : function() {
		  return this.regno = this.regno.replace(/[^0-9]/g, '');
	  },
	  wireTel : function() {
		  return this.wireTel = this.wireTel.replace(/[^0-9]/g, '');
	  }
  },
  mounted() {

  },
  methods: {
	 signUp () {
		//   validation

		//  validation 
		var defaultVali = this.defaultVali();
		if(!defaultVali) return false;

		//  validation 
		var regnoVali = this.regnoVali();
		if(!regnoVali) return false;

		this.signUpSubmit();
	 },
	 //  validation 
	 defaultVali() {
		if(this.password != this.passwordChk) {
			confirm.fnAlert("", "  .");
			this.password = "";
			this.passwordChk = "";
			return false;
		} else if ("" == this.smsCertifyYn) {
			confirm.fnAlert("", "SMS    .");
			return false;
		} else if ("" == this.phoneCerti && !this.phoneCertiChk) {
			confirm.fnAlert("", "    .");
			return false;
		} else if ("" == this.regNo) {
			confirm.fnAlert("", "  .");
			return false;
		} else if ("" == this.corpNm) {
			confirm.fnAlert("", " .");
			return false;	
		} else if ("" == this.ceoNm) {
			confirm.fnAlert("", " .");
			return false;
		} else if ("" == this.busiType) {
			confirm.fnAlert("", " .");
			return false;
		} else if ("" == this.busiClass) {
			confirm.fnAlert("", " .");
			return false;
		} else if ("" == this.woplaceAddress) {
			confirm.fnAlert("", " .");
			return false;
		} else if ("" == this.attachFileSeq) {
			confirm.fnAlert("", "  .");
			return false;
		} else if ("" == this.domainName) {
			confirm.fnAlert("", "  .");
			return false;
		} else if (!this.domainNameChk) {
			confirm.fnAlert("", "   .");
			return false;
		} else {
			return true;
		}
	 },

	 //   validation
	 regnoVali(value) {
		var valueMap = value.replace(/-/gi, '').split('').map(function(item) {
        	return parseInt(item, 10);
    	});

		if (valueMap.length === 10) {
			var multiply = new Array(1, 3, 7, 1, 3, 7, 1, 3, 5);
			var checkSum = 0;

			for (var i = 0; i < multiply.length; ++i) {
				checkSum += multiply[i] * valueMap[i];
			}

			checkSum += parseInt((multiply[8] * valueMap[8]) / 10, 10);
			return Math.floor(valueMap[9]) === (10 - (checkSum % 10));
		}

		return false;
	 },

	 //    function
	 domainNameChkBtn() {
		var vm = this;
		var params = {
          "domainName": this.domainName
        }
		var domainName = vm.domainName;

		//   3  validation 
		if(3>domainName.length) {
			confirm.fnAlert("", "  3  30  .");
			return false;
		}
        
		//     alert  domainNameChk   input disabled 
		signUpApi.domainChk(params).then(function(response) {
			var domainChkCnt = response.data;
			if(domainChkCnt>0) {
				confirm.fnAlert("", "   .   .");
			} else {
				confirm.fnAlert("", "  .");
				vm.domainNameChk = true;
			}
		});
	 },
	 //  ajax
	 signUpSubmit() {
		var params = {
			"loginId"				: this.loginId,
			"password"				: this.password,
			"smsCertifyYn"			: this.smsCertifyYn,
			"phoneCerti"			: this.phoneCerti,
			"regno"					: this.regno,
			"corpNm"				: this.corpNm,
			"ceoNm"					: this.ceoNm,
			"busiType"				: this.busiType,
			"busiClass"				: this.busiClass,
			"woplaceAddress"		: this.woplaceAddress,
			"woplaceAddressDetail"	: this.woplaceAddressDetail,
			"wireTel"				: this.wireTel,
			"attachFileSeq"			: this.attachFileSeq,
			"domainName"			: this.domainName
		}
		signUpApi.insertSignUp(params).then(function(response) {



		});
	 }
  }
}
</script>