<template>
  <div class="modal fade modalStyle in" id="myPagePopup" tabindex="-1" role="dialog" aria-labelledby="myModalLabel" aria-hidden="true">
    <div class="modal-dialog">
      <div class="modal-content">
        <div class="modal-body">
          <div class="of_h">
              <h5 class="lc-1"> </h5>
              <hr>
          </div>
          <div class="mt20">
            <h4 class="font-normal inline-block" style="width:23%">ID</h4>
            <h4 class="font-normal inline-block" style="width:75%">{{ userId }}</h4>
          </div>
          <div class="mt10">
        <h4 class="font-normal inline-block" style="width:23%"></h4>
        <input type="text" class="inputStyle height41" style="width:75%" title="" v-model="userName">
          </div>
          <div class="mt10">
            <h4 class="font-normal inline-block" style="width:23%; line-height: 35px;"></h4>
            <h4 class="font-normal inline-block" style="width:75%">  : {{ pwdUpdDt }}
              <button class="btnStyle3 gray font13 width120 ml10" title=" " @click="fnChgPwdDiv"> </button>
            </h4>
          </div>
          <div id="chgPwdDiv" style="display:none;">
            <div class="mt10">
              <h4 class="font-normal inline-block" style="width:23%"> </h4>
              <input type="password" class="inputStyle height41" style="width:75%" title="  " v-model="loginPwd" placeholder=" ">
            </div>
            <div class="mt10">
              <h4 class="font-normal inline-block" style="width:23%"> </h4>
              <input type="password" class="inputStyle height41" style="width:75%" title="   " v-model="chkLoginPwd" placeholder="  ">
            </div>
          </div>
          <div class="mt20">
            <h4 class="font-normal inline-block" style="width:23%"> </h4>
            <input type="text" class="inputStyle height41" style="width:56%" title="  " v-model="hpNumber" :disabled="true">
            <button class="btnStyle3 gray font13 width90 ml10 mt5" title=" " @click="fnChgHpNumDiv"> </button>
          </div>
          <div id="fnChgHpNumDiv" style="display:none;">
            <div class="mt10">
              <h4 class="font-normal inline-block" style="width:23%">  </h4>
              <input type="text" class="inputStyle height41" style="width:50%" title="   " v-model="chgHpNumber" placeholder="- ">
              <button class="btnStyle3 gray font13 width120 ml10 mt5" title=" "> </button>
            </div>
            <div class="mt10">
              <h4 class="font-normal inline-block" style="width:23%"></h4>
              <input type="text" class="inputStyle height41" style="width:75%" title=" " placeholder=" ." v-model="authNumber">
            </div>
          </div>
          <div class="text-right mt30">
            <button @click="fnSave" class="btnStyle3 black font14" title=""></button>
            <button @click="fnCloseLayer" class="btnStyle3 white font14 ml10" title=""></button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import confirm from "@/modules/commonUtil/service/confirm.js";
import {eventBus} from "@/modules/commonUtil/service/eventBus";
import myPageApi from '@/modules/myPage/service/myPageApi';

export default {
  name: 'myPagePopup',
  data() {
    return {
      userId    : '',
      userName  : '',
      pwdUpdDt  : '',
      hpNumber  : '',
      loginPwd  : '',
      chkLoginPwd : '',
      chgHpNumber : '',
      authNumber  : ''
    }
  },
  props: {
    layerView: {
			type: Boolean,
			require: true,
			default: false
		},
    memberInfo : {
      type : Object,
      require : true
    },
    popReset: {
      type: Number
    }
  },
  mounted() {
    this.fnReset();
  },
  watch: {
    popReset() {
      //     display 
      jQuery("#chgPwdDiv").css("display", "none");
      //     display 
      jQuery("#fnChgHpNumDiv").css("display", "none");
      this.fnReset();
    }
  },
  methods: {
      fnCloseLayer(){
          jQuery("#myPagePopup").modal("hide");
      },
      //  
      fnReset(){
        this.userId   = this.memberInfo.loginId;
        this.hpNumber = this.memberInfo.hpNumber;
        this.pwdUpdDt = this.memberInfo.pwdUpdDt;
        this.userName = this.memberInfo.userName;
      },
      //    
      fnChgHpNumDiv(){
        if(jQuery("#fnChgHpNumDiv").is(":visible")){
          this.loginPwd     = '',
          this.chkLoginPwd  = ''
          jQuery("#fnChgHpNumDiv").css("display", "none");
        } else {
          jQuery("#fnChgHpNumDiv").css("display", "block");
        }
      },
      //    
      fnChgPwdDiv(){
        if(jQuery("#chgPwdDiv").is(":visible")){
          this.loginPwd     = '',
          this.chkLoginPwd  = ''
          jQuery("#chgPwdDiv").css("display", "none");
        } else {
          jQuery("#chgPwdDiv").css("display", "block");
        }
      },
      // 
      fnSave(){
        if(this.loginPwd.trim().length > 0){
          if(this.loginPwd != this.chkLoginPwd){
            confirm.fnAlert("  .", "");
            return;
          }
        }

        var hpNumberTrim = this.chgHpNumber.trim();
        if(hpNumberTrim.length > 0){
          var reghpNumExp = RegExp(/^01(?:0|1|[6-9])(?:\d{3}|\d{4})\d{4}$/);
          if(reghpNumExp.test(hpNumberTrim) == false){
            confirm.fnAlert("", "   .");
            return;
          }
        } else {
          confirm.fnAlert("", "   .");
          return;
        }

        eventBus.$on('callbackEventBus', this.fnSaveCallBack);
        confirm.fnConfirm( "  ", "?", "");
      },
      fnSaveCallBack(){
        var params = {
          loginPwd : this.loginPwd.trim(),
          hpNumber : this.hpNumber.trim(),
          chgHpNumber : this.chgHpNumber.trim(),
          authNumber  : this.authNumber
        }

        myPageApi.saveMemberInfo(params).then(response =>{
        var result = response.data;
        if(result.success) {
          confirm.fnAlert( ".", "");
          this.fnCloseLayer();
        } else {
          confirm.fnAlert(result.message, "");
        }
      });
      }
  }
}
</script>
