<template>
	<div id="confirmCallback" class="modal fade modalStyle" tabindex="-1" role="dialog" aria-hidden="true" @click="fnClose">
		<div class="modal-dialog" style="width:1200px">
			<div class="modal-content">
				<div class="modal-body">
					<div>
						<h5 class="lc-1">  </h5>
						<hr>
						<div class="mt20 of_h">
							<div class="float-left" style="width:49%">
                 : {{this.row_data.brandName}}
							</div>
							<div class="of_h float-right" style="width:49%">
								ID : {{this.row_data.brandId}}
							</div>
						</div>
            <table cellspacing="0" id="list" class="table_skin1 tbl-striped" style="width:100%">
              <thead>
                <th>No.</th>
                <th></th>
                <th></th>
                <th></th>
                <th>SMSMO<br></th>
                <th></th>
                <th></th>
                <th></th>
                <th></th>
              </thead>
              <tbody>
                <tr v-for="(data, index) in callbackItems" :key="index">
                  <td>
                    {{ index + 1 }}
                  </td>
                  <td>
                    {{ data.PROJECT_NAME }}
                  </td>
                  <td>
                    {{ data.PROJECT_ID }}
                  </td>
                  <td>
                    {{ data.USE_CH_LIST }}
                  </td>
                  <td>
                    {{ data.REG_DT }}
                  </td>
                  <td>
                    {{ data.PAY_TYPE }}
                  </td>
                  <td>
                    {{ data.PROJECT_MEMBER_CNT }}
                  </td>
                  <td>
                    {{ data.REG_DT }}
                  </td>
                  <td>
                    {{ data.REG_DT }}
                  </td>
                </tr>
              </tbody>
            </table>
					</div>
					<div class="text-center mt40">
						<a @click="fnClose" class="btnStyle3 black font14" data-toggle="modal"></a>
					</div>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
import rcsApi from '../service/api'

export default {
  name: 'bpChanRcsTmpltCnt',
  data() {
    return {
      // select  value (  )
      selected : 10,
      //   
      pagingCnt : 1,
      //  
      callbackItems : [],
      count : 0
    }
  },
  props: {
    visibleCallback: {
      type: Boolean,
      require: true,
      default: false
    },
    row_data : {
      type: Object,
      require: false,
    }
  },
  watch: {
    visibleCallback: function(newVal, oldVal) {
      if(newVal) {
        jQuery("#confirmCallback").modal("show");
      } else {
        jQuery("#confirmCallback").modal("hide");
      }
    },
    row_data: function(newVal, oldVal) {
      this.fnSearchRegCallback();
    }
  },
  mounted() {
    var vm = this;
  },
  methods: {
    // 
    fnClose(){
      this.$emit('update:visibleCallback', false);
    },
    // 
    fnSearchRegCallback(){
      var params = {
          "brand_id"    : this.row_data.brandId,
          "rows"        : 15,
          "paging"      : 1
      };

      rcsApi.selectRcsRegTmpltList(params).then(response =>{
        this.callbackItems = response.data.data;
      });
    }
  }
}
</script>