<template>
  <!-- content -->
  <div class="row row-no-margin">

    <div class="contentHeader">
      <h2>SMS/MMS  </h2>
      <!-- <a href="#self" class="btnStyle2 backPink absolute top0 right0" onClick="window.location.reload()" title="   "> <i class="fal fa-book-open"></i></a> -->
    </div>
  
    <!--  -->
    <div class="row">
      <div class="col-xs-12">
        <div class="menuBox">
          <div class="of_h">
            <div class="inline-block" style="width:8%"><h4></h4></div>
            <div class="inline-block" style="width:91%">
              <select class="selectStyle2" style="width:15%" title=" " v-model="searchData.searchCondi">
                <option value="tmpltName"></option>
                <option value="tmpltId">ID</option>
              </select>
              <input type="text" class="inputStyle vertical-top ml10" style="width:37.5%" title=" " v-model="searchData.searchText">
            </div>
          </div>
          <div class="of_h consolMarginTop">
            <div class="inline-block" style="width:8%"><h4></h4></div>
            <div class="inline-block" style="width:91%">
              <Calendar @update-date="fnUpdateStartDate" calendarId="searchStartDate" classProps="datepicker inputStyle maxWidth200" :initDate="searchData.searchStartDate"></Calendar>
              <span style="padding:0 11px">~</span>
              <Calendar @update-date="fnUpdateEndDate" calendarId="searchEndDate" classProps="datepicker inputStyle maxWidth200" :initDate="searchData.searchEndDate"></Calendar>
              <ul class="tab_s2 ml20">
                <li :class="this.searchDateInterval==0 ? 'active' : ''"><a @click="fnSetIntervalSearchDate(0);" title="   "></a></li>
                <li :class="this.searchDateInterval==7 ? 'active' : ''"><a @click="fnSetIntervalSearchDate(7);" title="1  ">1</a></li>
                <li :class="this.searchDateInterval==15 ? 'active' : ''"><a @click="fnSetIntervalSearchDate(15);" title="15  ">15</a></li>
                <li :class="this.searchDateInterval==30 ? 'active' : ''"><a @click="fnSetIntervalSearchDate(30);" title="1  ">1</a></li>
              </ul>
            </div>
          </div>

          <div class="of_h">
            <div class="inline-block" style="width:16%"><h4>  </h4></div>
            <div class="inline-block" style="width:83%">
              <div class="consolCheck consolMarginTop">
                <input type="checkbox" id="otherUse_all" class="checkStyle2" @change="fnSearchOthPrjUseYnChkAll" v-model="othPrjUseYnAllSelected">
                <label for="otherUse_all" class="mr30"></label>
                <input type="checkbox" id="searchOthPrjUseYn_Y" class="checkStyle2" value="Y" v-model="searchData.searchOthPrjUseYn">
                <label for="searchOthPrjUseYn_Y" class="mr30"></label>
                <input type="checkbox" id="searchOthPrjUseYn_N" class="checkStyle2" value="N" v-model="searchData.searchOthPrjUseYn">
                <label for="searchOthPrjUseYn_N" class="mr30"></label>
              </div>
              <!-- <a @click="fnPageNoResetSearch()" class="btnStyle1 float-right" title=" "></a> -->
              <a @click="fnSelectSmsTmpltList()" class="btnStyle1 float-right" title=""></a>
            </div>
          </div>
        </div>
      </div>
    </div>

    <div class="row">
      <div class="col-xs-12 consolMarginTop">
        <div class="of_h inline">
          <div class="float-right">
            <router-link :to="{ name: 'smsTemplateManage' }" tag="a" class="btnStyle2 backBlack" title=" "> </router-link>
            <a @click="fnDeleteSmsTemplate" class="btnStyle2 borderGray ml10" title=""></a>
            <a @click="fnExcelDownLoad" class="btnStyle2 borderGray ml10" title=" ">  <i class="fal fa-arrow-to-bottom"></i></a>
          </div>
        </div>
      
        <!-- 15  -->
        <div class="of_h inline">
          <div class="float-left"> : <span class="color1"><strong>{{totCnt}}</strong></span>
            <SelectLayer @fnSelected="fnSelected" classProps="selectStyle2 width120 ml20"></SelectLayer>
          </div>
        </div>
        <!-- //15  -->
        
        <div class="row">
          <div class="col-xs-12 consolMarginTop">
            <!-- table -->
            <table class="table_skin1 bt-000 tbl-striped">
              <colgroup>
                <col style="width:5%">
                <col style="width:5%">
                <col>
                <col style="width:17%">
                <col style="width:17%">
                <col style="width:10%">
                <col style="width:10%">
                <col style="width:17%">
              </colgroup>
              <thead>
                <tr>
                  <th class="text-center lc-1">
                    <input type="checkbox" id="listCheck_all" class="boardCheckStyle" value="listCheck_all" @change="fnListChkAll" v-model="listAllChecked">
                    <label for="listCheck_all"></label>
                  </th>
                  <th class="text-center lc-1">No.</th>
                  <th class="text-center lc-1"> ID</th>
                  <th class="text-center lc-1"></th>
                  <th class="text-center lc-1">  </th>
                  <th class="text-center lc-1"></th>
                  <th class="text-center lc-1"></th>
                  <th class="text-center lc-1 end"></th>
                </tr>
              </thead> 
              <tbody>
                <tr v-for="(contant, idx) in contants" :key="contant.tmpltId">
                  <td class="text-center">
                    <input type="checkbox" :id="'listCheck_'+idx" name="listCheck_" class="boardCheckStyle" :value="contant.tmpltId" v-model="listChkBox">
                    <label :for="'listCheck_'+idx"></label>
                  </td>
                  <td class="text-center">{{totCnt-offset-contant.rownum+1}}</td>
                  <td class="text-center">
                    <router-link :to="{ name: 'smsTemplateManage', params: { tmpltId: contant.tmpltId }}">{{contant.tmpltId}}</router-link>
                  </td>
                  <td class="text-center">{{contant.tmpltName}}</td>
                  <td class="text-center">{{contant.projectIdNm}}</td>
                  <td class="text-center">{{contant.senderType}}</td>
                  <td class="text-center">{{contant.regNm}}</td>
                  <td class="text-center end">{{contant.regDt}}</td>
                </tr>
                <tr v-if="contants.length == 0">
                  <td class="text-center">
                    <input type="checkbox" id="listCheck_0" class="boardCheckStyle">
                    <label for="listCheck_0"></label>
                  </td>
                  <td class="text-center" colspan="7">  .</td>
                </tr>
              </tbody>
            </table>
            <!-- //table -->
          </div>
        </div>
      </div>
    </div>
    
    <!-- pagination -->
    <div id="pageContent">
      <PageLayer @fnClick="fnSearch" :listTotalCnt="totCnt" :selected="listSize" :pageNum="pageNo" ref="updatePaging"></PageLayer>
    </div>
    <!-- //pagination -->

  </div>
</template>

<script>
import PageLayer from '@/components/PageLayer.vue';
import SelectLayer from '@/components/SelectLayer.vue';
import Calendar from "@/components/Calendar.vue";

import templateApi from "@/modules/template/service/templateApi.js";
import confirm from "@/modules/commonUtil/service/confirm.js";
import {eventBus} from "@/modules/commonUtil/service/eventBus";

export default {
  name: "pushTemplateList",
  components: {
    SelectLayer,
    PageLayer,
    Calendar
  },
  props: {
    searchData : {
      type: Object,
      require: false,
      default: function() {
        return {
          'searchCondi' : 'tmpltName',
          'searchText' : '',
          'searchStartDate' : this.$gfnCommonUtils.getCurretDate(),
          'searchEndDate' : this.$gfnCommonUtils.getCurretDate(),
          'searchOthPrjUseYn' : []
        }
      }
    },
    componentsTitle: {
      type: String,
      require: false,
      default: function() {
        return 'SMS/MMS ';
      }
    },
  },
  data() {
    return {
      othPrjUseYnAllSelected: false,
      listAllChecked: false,
      listChkBox: [],
      listSize : 10,  // select  value (  )
      pageNo : 1,  //   
      totCnt : 0,  //  
      offset : 0, // 
      searchDateInterval: 7,
      contants: []
    }
  },
  mounted() {
    this.fnSetIntervalSearchDate(this.searchDateInterval);
    this.fnPageNoResetSearch();
  },
  methods: {
    //  
    fnExcelDownLoad(){
      const params = this.searchData;
      templateApi.excelDownloadSmsTmplt(params);
    },
    // 
    fnDeleteSmsTemplate(){
      // 
      if(this.listChkBox == null || this.listChkBox.length == 0){
        confirm.fnAlert(this.componentsTitle, '  .');
        return;
      }

      eventBus.$on('callbackEventBus', this.fnProcDeleteSmsTemplate);
      confirm.fnConfirm(this.componentsTitle, "  ?", "");
    },
    //  
    async fnProcDeleteSmsTemplate(){
      const params = {tmpltIds : this.listChkBox};
      await templateApi.deleteSmsTmplt(params).then(response =>{
        const result = response.data;
        if(result.success) {
          confirm.fnAlert(this.componentsTitle, '.');
          this.listChkBox = [];
          this.fnPageNoResetSearch();
        } else {
          confirm.fnAlert(this.componentsTitle, result.message);
        }
      });
    },
    //
    fnSetIntervalSearchDate(interval){
      this.searchDateInterval = interval;
      this.searchData.searchEndDate = this.$gfnCommonUtils.getCurretDate();
      this.searchData.searchStartDate = this.$gfnCommonUtils.strDateAddDay(this.searchData.searchEndDate, -this.searchDateInterval);
    },
    fnUpdateStartDate(sltDate) {
      this.searchData.searchStartDate = sltDate;
    },
    fnUpdateEndDate(sltDate) {
      this.searchData.searchEndDate = sltDate;
    },
    //    
    fnSearchOthPrjUseYnChkAll(){
      if(this.othPrjUseYnAllSelected){
        this.searchData.searchOthPrjUseYn = ['Y', 'N'];
      } else {
        this.searchData.searchOthPrjUseYn = [];
      }
    },
    //  
    async fnSelectSmsTmpltList(){
      // 
      if(this.searchData.searchStartDate && this.searchData.searchEndDate){
        if(this.searchData.searchStartDate.replace(/[^0-9]/g, '') > this.searchData.searchEndDate.replace(/[^0-9]/g, '')){
          confirm.fnAlert(this.componentsTitle, '    .');
          return false;
        }
      }

      let params = Object.assign({}, this.searchData);
      params.pageNo = this.pageNo;
      params.listSize = this.listSize;
      await templateApi.selectSmsTmpltList(params).then(response =>{
        const result = response.data;
        if(result.success) {
          this.contants = result.data;
          this.totCnt = result.pageInfo.totCnt;
          this.offset = result.pageInfo.offset;
        } else {
          confirm.fnAlert(this.componentsTitle, result.message);
        }
      });
    },
    //  
    fnListChkAll(){
      const vm = this;
      if(this.listAllChecked){
        this.contants.forEach(function(contant){
          vm.listChkBox.push(contant.tmpltId);
        });
      } else {
        this.listChkBox = [];
      }
    },
    // select    
    fnSelected(listSize) {
      this.listSize = Number(listSize);
      this.$refs.updatePaging.fnAllDecrease();
    },
    fnPageNoResetSearch(){
      this.$refs.updatePaging.fnAllDecrease();
    },
    fnSearch(pageNo) {
      this.pageNo = (this.$gfnCommonUtils.defaultIfEmpty(pageNo, '1'))*1;
      this.fnSelectSmsTmpltList();
    }
  }
}
</script>