              <tr>
              <td class="text-left color4">  </td>
              <td class="text-right end">10,000</td>
              </tr>	
              <tr>
              <td class="text-left color4 bgColor_sky"> </td>
              <td class="text-right end bgColor_sky">1,500,000</td>
              </tr>	
            </tbody>
          </table>
          <!-- //table -->
        </div>	
        <div class="col-xs-6 text-center" style="padding: 2.5% 0">	
          <a @click="fnTossPay" class="btnStyle1"> </a>
        </div>
      </div>
    </div>

    <h4> </h4>
    <div class="row">
      <div class="col-xs-12">
        <PagingCnt :pageInfo.sync="pageInfo" />
        
        <!-- table -->
        <table class="table_skin1 bt-000 tbl-striped">
          <colgroup>
            <col style="width:10%">
