					</div>						
					<div class="text-center mt20">
						<a @click="fnApproval" class="btnStyle1 backBlack"></a>
						<a @click="fnCloseLayer" class="btnStyle1 backWhite"></a>						
					</div>
				</div>
			</div>
		</div>
</div>
</template>

<script>
import axios from 'axios'
import tokenSvc from '@/common/token-service';
import projectApi from '../service/projectApi'

export default {
  name: 'preRegExPop',
  data() {
    return {
      brandId   : "",
      brandList : [
        {
          // 
          "brandId" : "BR.xphojy",
          "brandName" : "brandName2"
        }
      ],
      chatbotCnt : 0,
      chatbots  : [],
      mainMdn   : "",
      mainTitle : "",
      rcsReply  : "",
      rcsReplyYn : ""
    }
  },
  mounted() {
    //this.fnBrandList();
  },
  props: {
    projectId : {
      type: String,
      require: true
    }
  },
  methods: {
    // 
    fnCloseLayer: function() {
      jQuery("#regPop").modal("hide");
    },
    fnBrandList(){
      var params = { "projectId" : this.projectId }

      projectApi.selectBrandList(params).then(response =>{
        this.brandList = response.data.data;
      });
    },
    // 
    fnAddChatbotTr(){
      this.chatbotCnt++;
      this.chatbots.push({
        "mdn"       : "",		  // 
        "rcsReply"	: "1",		//   0 = x / 1 = o
        "subTitle"	: "",		  // 
        "service"   : "a2p",	// a2p 
        "display"   : "10"		// '10' 
      });
    },
    // 
    fnDeleteChatbotTr(){
      this.chatbotCnt--;
      this.chatbots.splice(-1);
    },
    // 
    async fnApproval(sts){
      //  
     /*  if( !this.fnValidate() ){
        return;
      } */

      var fd = new FormData();
      fd.append('loginId'			, tokenSvc.getToken().principal.userId);
      fd.append('corpId'			, tokenSvc.getToken().principal.corpId);
      fd.append('projectId'		, this.projectId);
      fd.append('brandId'			, this.brandId);
      fd.append('mainMdn'			, this.mainMdn);
      fd.append('mainTitle'		, this.mainTitle);

      //  
      fd.append('certiFile'		, this.$refs.certiImgFile.files[0]);

      // () 
      var mainRcsReplyYn = this.rcsReplyYn;
      
      if( mainRcsReplyYn ){ this.rcsReply = 1 } else { this.rcsReply = 0 }
      //   
      this.chatbots.push({
        "mdn"	    	: this.mainMdn,		// 
        "rcsReply"	: this.rcsReply,		//   0 = x / 1 = o
        "subTitle"	: this.mainTitle,		// 
        "service"	  : "a2p",	// a2p 
        "display"	  : "10"		// '10' 
      });

      var list = [];

      for( var i = 0; i < this.chatbots.length; i++ ){
        var obj = JSON.stringify(this.chatbots[i]);
        list.push(obj);
      }
      var listString = list.join(", ");
      fd.append('chatbots'		, listString) ;

        
      await axios.post('/projectApi/manage/saveRcsChatbotReqForApi',
        fd, {headers: {'Content-Type': 'multipart/form-data'}
      }).then( response => {
        var result = response.data;
        if(result.success) {
          confirm.fnAlert("", " .");
          //  
          this.$refs.backBtn.click();
        } else {
          confirm.fnAlert("", result.message);
        }
      }).catch(function () {
        confirm.fnAlert("", " .???");
      });
    }
  }
}
</script>
