<template>
	<div id="layerPopup" class="modal fade modalStyle" tabindex="-1" role="dialog" aria-hidden="true" >
		<div class="modal-dialog">
			<div class="modal-content">
				<div class="modal-body">
					<div>
						<h5 class="lc-1">MO  </h5>
						<hr>
						<div class="mt20">
							<div>
								<h4 class="font-normal inline-block width120">API KEY</h4>
								<input type="text" class="inputStyle" style="width:76%" v-model="apiKey" disabled>
							</div>
							<div class="mt10">
								<h4 class="font-normal inline-block width120">MO </h4>
								<input type="text" class="inputStyle" style="width:76%" v-model="moNumber">
							</div>
							<div class="mt20">
								<h4 class="font-normal inline-block width120">MO </h4>
								<div class="inline-block">
									<input type="radio" id="SMS" value="SMSMO" class="cBox" v-model="moType"> <label for="SMS" class="payment mr30 font-size12">SMS MO</label>
									<input type="radio" id="MMS" value="MMSMO" class="cBox" v-model="moType"> <label for="MMS" class="payment font-size12">MMS MO</label>		
								</div>
							</div>
							<div class="mt20">
								<h4 class="font-normal inline-block width120"> <br></h4>
								<div class="inline-block">
									<input type="radio" id="ALL" value="ALL" class="cBox" v-model="pjtAllNo"> <label for="ALL" class="payment mr30"></label>
									<input type="radio" id="NO" value="NO" class="cBox" v-model="pjtAllNo"> <label for="NO" class="payment"></label>		
								</div>
							</div>
							<p class="mt30 lc-1 Modaltext font-size12" style="margin:0">
                <i class="far fa-info-circle"></i> MO  : 
                <br>
                <i class="far fa-info-circle"></i>  : SMS 7/LMS 20/MMS 50
              </p>
						</div>
					
					</div>
					<div class="text-center mt40">
						<a @click="fnSave" class="btnStyle3 black font14" data-toggle="modal"></a>
						<a @click="fnClose" ref="closeBtn" class="btnStyle3 white font14" data-dismiss="modal"></a>						
					</div>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
import api from '../service/api'
import confirm from "@/modules/commonUtil/service/confirm"

export default {
  name: 'bpChanMo',
  data() {
    return {
      apiKey    : "",
      moNumber  : "",
      moType    : "",
      pjtAllNo  : ""
    }
  },
  props: {
    save_status: {
      type: String,
      require: true,
      default: 'C'
    },
    row_data : {
      type: Object,
      require: false,
    },
    projectId : {
      type : String,
      require : true
    }
  },
  watch: {
    row_data: function(newVal, oldVal) {
      this.apiKey = newVal.apiKey;
      this.moNumber = newVal.moNumber;
      this.moType = newVal.moType;
      this.pjtAllNo = newVal.pjtAllNo;
    }
  },
  mounted() {
    
  },
  methods: {
    // 
    fnClose(){
      jQuery("#layerPopup").modal('hide');
    },
    // 
    fnSave(){
      //    
      var projectStr = 'ALL';
      if(this.pjtAllNo != 'ALL' ){
        projectStr = this.projectId;
      }

      var params = {
        "sts"         : this.save_status,
        "apiKey"      : this.apiKey,
        "moNumber"    : this.moNumber,
        "moType"      : this.moType,
        "projectId"   : projectStr
      }

      api.saveMoCallback(params).then(response =>{
        var result = response.data;

        if(result.success) {
          confirm.fnAlert("", ".");
          //  
          this.$refs.closeBtn.click();
          //   
          this.$parent.fnSearch();
          // 
        } else {
          confirm.fnAlert("", result.message);
        }
      });
    }
  }
}
</script>