<template>
<div class="row row-no-margin">
  <div class="contentHeader">
      <h2> >  </h2>
      <a href="#self" class="btnStyle2 backPink absolute top0 right0" onClick="window.location.reload()" title="  "> <i class="fal fa-book-open"></i></a>
  </div>
  <!--  -->
  <div class="row">
    <div class="col-xs-12">
      <div class="menuBox">

        <div class="of_h">
          <div class="inline-block" style="width:8%"><h4 class="font-normal mt15"></h4></div>
          <div class="inline-block" style="width:91%">
            <select v-model="searchData.searchCondi" class="selectStyle2" style="width:15%" title="">
                <option value="templateName"></option>
                <option value="templateChannel"></option>
            </select>
            <input type="text" class="inputStyle vertical-top ml10" id="searchText" name="searchText" v-model="searchData.searchText" style="width:37.5%" title="">
          </div>
        </div>
        <div class="of_h consolMarginTop">
          <div class="inline-block" style="width:8%"><h4 class="font-normal mt15"></h4></div>
          <div class="inline-block" style="width:91%">
            <Calendar @update-date="fnUpdateStartDate" calendarId="searchStartDate" classProps="datepicker inputStyle maxWidth200" :initDate="searchData.searchStartDate" ></Calendar>
            <span style="padding:0 11px">~</span>
            <Calendar @update-date="fnUpdateEndDate" calendarId="searchEndDate" classProps="datepicker inputStyle maxWidth200" :initDate="searchData.searchEndDate"></Calendar>
            <ul class="tab_s2 ml20">
                <li :class="this.searchDateInterval==0 ? 'active' : ''"><a @click="fnSetIntervalSearchDate(0);" title="   "></a></li>
                <li :class="this.searchDateInterval==7 ? 'active' : ''"><a @click="fnSetIntervalSearchDate(7);" title="1  ">1</a></li>
                <li :class="this.searchDateInterval==15 ? 'active' : ''"><a @click="fnSetIntervalSearchDate(15);" title="15  ">15</a></li>
                <li :class="this.searchDateInterval==30 ? 'active' : ''"><a @click="fnSetIntervalSearchDate(30);" title="1  ">1</a></li>
            </ul>
            <a @click="fnSearch" class="btnStyle2 float-right" title=""></a>
          </div>
        </div>
      </div>
    </div>
  </div>


  <!--  -->
  <div class="row">
    <div class="col-xs-12 consolMarginTop">
      <div class="of_h inline">
        <div class="float-right">
          <!-- <a @click="fRegist" class="btnStyle2 borderGray" title=" ">  <i class="fal fa-arrow-to-bottom"></i></a> -->
          <router-link :to="{ name: 'integratedTemplateManage' }" tag="a" class="btnStyle2 borderGray" > <i class="fal fa-arrow-to-bottom"></i></router-link>
          <a @click="fnDelete" class="btnStyle2 borderGray" title=""> <i class="fal fa-arrow-to-bottom"></i></a>
          <a @click="fnExcelDownLoad" class="btnStyle2 borderGray" title=" ">  <i class="fal fa-arrow-to-bottom"></i></a>
        </div>
      </div>

      <!-- 15  -->
      <div class="of_h inline">
        <div class="float-left"> : <span class="color1"><strong>{{totCnt}}</strong></span>
          <SelectLayer @fnSelected="fnSelected"></SelectLayer>
        </div>
      </div>
      <!-- //15  -->

      <div class="row">
        <div class="col-xs-12 consolMarginTop">
          <!-- table -->
          <table class="table_skin1 bt-000 tbl-striped">
            <colgroup>
              <col style="width:5%">
              <col style="width:5%">
              <col>
              <col style="width:10%">
              <col style="width:12%">
              <col style="width:7%">
              <col style="width:7%">
              <col style="width:12%">
              <col style="width:7%">
              <col style="width:7%">
              <col style="width:12%">
            </colgroup>
            <thead>
              <tr>
                <th class="text-center lc-1"><input type="checkbox" id="listCheck_all" class="boardCheckStyle" value="listCheck_all"><label for="listCheck_all"></label></th>
                <th class="text-center lc-1">No.</th>
                <th class="text-center lc-1"> ID</th>
                <th class="text-center lc-1"></th>
                <th class="text-center lc-1"> </th>
                <th class="text-center lc-1"> </th>
                <th class="text-center lc-1"> </th>
                <th class="text-center lc-1">  </th>
                <th class="text-center lc-1"></th>
                <th class="text-center lc-1"></th>
                <th class="text-center lc-1 end"></th>
              </tr>
            </thead>
            <tbody>
              <tr v-for="(data, idx) in datas" :key="data.row_num">
                <td class="text-center"><input type="checkbox" :id="'listCheck_'+idx" class="boardCheckStyle" :value="data.tmpltCode" v-model="listChkBox">
                <label :for="'listCheck_'+idx"></label></td>
                <td>{{ idx + 1 }}</td>
                <!-- <td class="text-center"><a href="javascript:void(0);" @click="fnDetailPop(idx)">{{data.phoneNumber}}</a></td> -->
                <td class="text-center">{{data.tmpltCode}}</td>
                <td class="text-center">{{data.tmpltTitle}}</td>
                <td class="text-center">{{data.tmpltChannel}}</td>
                <td class="text-center">{{data.msgKind}}</td>
                <td class="text-center">{{data.msgType}}</td>
                <td class="text-center">{{data.otherProjectUseYn}}</td>
                <td class="text-center">{{data.useYn}}</td>
                <td class="text-center">{{data.regId}}</td>
                <td class="text-center end">{{data.regDt}}</td>
              </tr>
              <tr v-if="datas.length == 0">
                  <td class="text-center" colspan="11">  .</td>
              </tr>
            </tbody>
          </table>
          <!-- //table -->
        </div>                  
      </div>
    </div>
  </div>
  <!-- pagination -->
  <div id="pageContent">
      <PageLayer @fnClick="fnSearch" :listTotalCnt="totCnt" :selected="listSize" :pageNum="pageNo" ref="updatePaging"></PageLayer>
  </div>
</div>
</template>


<script>
import integratedTemplateApi from '../service/integratedTemplateApi'
import PageLayer from '@/components/PageLayer.vue';
import SelectLayer from '@/components/SelectLayer.vue';
import Calendar from "@/components/Calendar.vue";
import tokenSvc from '@/common/token-service';
import confirm from "@/modules/commonUtil/service/confirm.js";
import {eventBus} from "@/modules/commonUtil/service/eventBus";

export default {
  components: {
    SelectLayer,
    PageLayer,
    Calendar
  },
  props: {
    searchData : {
      type: Object,
      require: false,
      default: function() {
        return {
          'searchCondi' : 'templateName',
          'searchText' : '',
          'searchStartDate' : this.$gfnCommonUtils.getCurretDate(),
          'searchEndDate' : this.$gfnCommonUtils.getCurretDate()
        }
      }
    },
    componentsTitle: {
      type: String,
      require: false,
      default: function() {
        return '  ';
      }
    },
  },
  data() {
    return {
      listAllChecked: false,
      listChkBox: [],
      listSize : 10,  // select  value (  )
      pageNo : 1,  //   
      totCnt : 0,  //  
      offset : 0, // 
      searchDateInterval: 7,
      datas: []
    }
    
        
  },
  mounted() {
    this.fnSetIntervalSearchDate(this.searchDateInterval);
    this.fnSearch();
  },
  methods: {
    //
    fnSetIntervalSearchDate(interval){
      this.searchDateInterval = interval;
      this.searchData.searchEndDate = this.$gfnCommonUtils.getCurretDate();
      this.searchData.searchStartDate = this.$gfnCommonUtils.strDateAddDay(this.searchData.searchEndDate, -this.searchDateInterval);
    },
    fnUpdateStartDate(sltDate) {
      this.searchData.searchStartDate = sltDate;
    },
    fnUpdateEndDate(sltDate) {
      this.searchData.searchEndDate = sltDate;
    },

    // 
    fnDelete(){
      // 
      if(this.listChkBox == null || this.listChkBox.length == 0){
        confirm.fnAlert(this.componentsTitle, '  .');
        return;
      }

      eventBus.$on('callbackEventBus', this.fnProcDeleteIntegratedTemplate);
      confirm.fnConfirm(this.componentsTitle, "  ?", "");
    },

    //   
    async fnProcDeleteIntegratedTemplate(){
      var params = {'tmpltCode' : this.listChkBox};
      await integratedTemplateApi.deleteIntegratedTemplate(params).then(response =>{
        var result = response.data;
        if(result.success) {
          confirm.fnAlert(this.componentsTitle, '.');
          this.listChkBox = [];
          this.fnSearch();
        } else {
          confirm.fnAlert(this.componentsTitle, result.message);
        }
      });
    },


    // 
    fnExcelDownLoad(){
      var params = this.searchData;
      integratedTemplateApi.excelDownloadIntegratedTemplate(params);
    },

    // 
    async fnSelectIntegratedTemplateList() {

      // 
      if(this.searchData.searchStartDate && this.searchData.searchEndDate){
        if(this.searchData.searchStartDate.replace(/[^0-9]/g, '') > this.searchData.searchEndDate.replace(/[^0-9]/g, '')){
          alert('    .');
          return false;
        }
      }
          
      var params = Object.assign({}, this.searchData);
      params.pageNo = this.pageNo;
      params.listSize = this.listSize;

      params.loginId = tokenSvc.getToken().principal.userId;
      params.roleCd = tokenSvc.getToken().principal.roleCd

      await integratedTemplateApi.selectIntegratedTemplateList(params).then(response =>{
        var result = response.data;
        if(result.success) {
          this.datas = result.data;
          this.totCnt = result.pageDto.totCnt;
          this.offset = result.pageDto.offset;
        } else {
          alert(result.message);
        }
      });
    },
 
    //  
    fnListChkAll(){
      var vm = this;
      if(this.listAllChecked){
        this.datas.forEach(function(data){
          vm.listChkBox.push(data.tmpltCode);
        });
      } else {
        this.listChkBox = []
      }
    },

    // select    
    fnSelected(listSize) {
      this.listSize = Number(listSize);
      this.$refs.updatePaging.fnAllDecrease();
    },
    fnSearch(pageNum) {
      this.pageNo = (this.$gfnCommonUtils.defaultIfEmpty(pageNum, '1'))*1;
      //this.fnSelectIntegratedTemplateList();
    }
  }
}
</script>