<template>
	<!-- stop Modal -->
	<div v-if="layerView" class="layerPopup">
		<!--div class="modal fade modalStyle" id="stop" tabindex="-1" role="dialog" aria-hidden="true"-->
			<div class="modal-dialog">
				<div class="modal-content">
					<div class="modal-body">
						<h2></h2>
						<p class="text-center mt20">   Console     .</p>
						<div class="text-center mt20">
							<a @click="fnStopUser()" class="btnStyle1 backBlack mr5" title=""> </a>
							<a @click="fnCloseLayer()" class="btnStyle1 backWhite" title=""></a>
						</div>
					</div>
				</div>
			</div>
		<!--/div-->
	</div>
</template>

<script>
import userApi from '../service/userApi'
import confirm from "@/modules/commonUtil/service/confirm";

export default {
	name: 'StopLayer',
	props: {
		layerView: {
			type: Boolean,
			require: true,
			default: false
		},
		title: {
			type: String,
			require: false
		},
		stopUserId: {
			type: String,
			require: true
		}
	},
	methods: {
		// 
		fnCloseLayer(){
			this.$emit('update:layerView', false);
		},
		//
		fnStopUser() {
			console.log("userId : " + this.stopUserId);
			var params = {
				"userId":this.stopUserId
			}
			userApi.stopUser(params).then(response =>{
				var result = response.data;
				if(result.success) {
					confirm.fnAlert("", " .");
					this.$parent.fnSelectUserList();
				} else {
					confirm.fnAlert("", result.message);
				}
			});
			this.fnCloseLayer();
		}
	}
}
</script>