<template>
<div v-if="layerView" class="layerPopup">
	<!-- <div class="modal fade modalStyle" id="cancel" tabindex="-1" role="dialog" aria-hidden="true"> -->
		<div class="modal-dialog">
			<div class="modal-content">
				<div class="modal-body">
					<div class="of_h">
						<h2> </h2>
						<hr>						
						<div class="of_h">
							<div class="float-left" style="width:15%"><h5></h5></div>
							<div class="float-right" style="width:84%">
								<textarea class="textareaStyle height120" placeholder="" v-model="msg"></textarea>
							</div>							
						</div>
						<div class="of_h consolMarginTop">
							<div class="float-left" style="width:15%"><h5></h5></div>
							<div class="float-right" style="width:84%">
								<textarea class="textareaStyle height120" placeholder="" v-model="rowData.cancelCause" ></textarea>
							</div>							
						</div>				
									
					</div>						
					<div class="text-center mt20">
						<a href="#self" class="btnStyle1 backBlack" @click.self="fnCancelLayer" title=""></a>
						<a href="#self" class="btnStyle1 backWhite" data-dismiss="modal" @click.self="fnCloseLayer" title=""></a>						
					</div>
				</div>
			</div>
		</div>
	<!-- </div> -->
</div>
</template>

<script>
import messageStatusApi from '../service/messageStatusApi';
import confirm from "@/modules/commonUtil/service/confirm.js";
import {eventBus} from "@/modules/commonUtil/service/eventBus";

export default {

  name: 'UpdateLayer',
  props: {//    require: true =>  
    rowData : {
      type: Object,
      require: false,
      default: function() {
        return {'cancelCause':''}
      }
    },
    layerView: {
      type: Boolean,
      require: true,
      default: false
    },
    detailTitle: {
      type: String,
      require: true
    },
    detailWebReqId: {
      type: String,
      require: true
    },
    detailMsg: {
      type: String,
      require: true
    }
  },
  data() {// 
    return {
	    listSize : 10,  // select  value (  )
      pageNo : 1,  //   
      totCnt : 0,  //  
	    offset : 0, // 
	    msg : ''
    }
  },

  watch: {
    layerView: function(newVal) {
        // LayerView true         .
        if(newVal) {
          this.msg = this.detailMsg;
        }
    },
  },

methods: {
	  fnCancelLayer(){
      eventBus.$on('callbackEventBus', this.fnProcBookingSendCancel);
      confirm.fnConfirm(this.detailTitle, "  ?", "");
    },
    async fnProcBookingSendCancel(){
      
      var params = this.rowData;
      params.webReqId = this.detailWebReqId;

      await messageStatusApi.cancelBookingSend(params).then(response =>{
        var result = response.data;
        if(result.success) {
          confirm.fnAlert(this.detailTitle, '.');
          //this.listChkBox = [];
          this.fnCloseLayer();
          //this.$router.push('bookingSendList');
          this.$parent.fnSelectBooingSendList();
        } else {
          confirm.fnAlert(this.detailTitle, result.message);
        }
      });
    },
    // 
    fnCloseLayer(){
      this.$emit('update:layerView', false);
    },
    fnBack(){
      this.$router.go(-1);
    }
  }
}
</script>
