<template>
  <div>
		<article>
			<h4 class="inline-block">Push APP ID </h4>
			<div class="float-right h4Button">
				<a @click="fnReg" class="btnStyle2 borderGray">  </a>
			</div>
			<p class="color3"><i class="far fa-info-circle"></i> APP ID 5   ,     .</p>

			<div class="row">
				<div class="col-xs-12 consolMarginTop">
					<!-- table -->
					<table  class="table_skin1 bt-000 tbl-striped">
						<colgroup>
              <col style="width:15%">
							<col>
							<col style="width:15%">
							<col style="width:15%">
              <col style="width:15%">
							<col style="width:10%">
							<col style="width:10%">
						</colgroup>
						<thead>
							<tr>
                <th class="text-center lc-1">APP </th>
                <th class="text-center lc-1">APP ID</th>
                <th class="text-center lc-1">FCM  </th>
                <th class="text-center lc-1">APNS  </th>
                <th class="text-center lc-1">  </th>
                <th class="text-center lc-1"></th>
                <th class="text-center lc-1 end"></th>
							</tr>
						</thead>
						<tbody>
							<tr v-for="(row, index) in data" :key="index">
                <td class="text-center">{{row.appNm}}</td>
                <td class="text-center">{{row.appId}}</td>
                <td class="text-center">{{row.fcmYn}}</td>
                <td class="text-center">{{row.apnsYn}}</td>
                <td class="text-center">{{row.otherProjectYn}}</td>
                <td class="text-center">{{row.regDt}}</td>
                <td class="end">
                  <a @click="fnMod(row)" class="btnStyle1 borderLightGray small mr5"></a>
                  <a @click="fnDeleteConfirm(row)" class="btnStyle1 borderLightGray small mr5"></a>
                </td>
							</tr>
						</tbody>
					</table>
					<!-- //table -->
				</div>			
			</div>
		</article>
	</div>
</template>


<script>
import api from '../service/api'

import tokenSvc from '@/common/token-service';

import {eventBus} from "@/modules/commonUtil/service/eventBus";
import confirm from "@/modules/commonUtil/service/confirm"

export default {
  components: {
    api
  },
  data() {
    return {
      //  
      projectId   : '',
      projectName : '',
      rowData     : {},
      data        : {}
    }
  },
  mounted() {
    this.projectId    = this.$parent.projectId;
    this.projectName  = this.$parent.projectName;
    this.fnSearch();
  },
  methods: {
    // 
    fnSearch() {
      var params = {
        "projectId" : this.projectId,
        "loginId"   : tokenSvc.getToken().principal.loginId,
      }
       
      api.selectPushManageList(params).then(response =>{
        this.data = response.data.data;
      });
    },
    //    
    fnReg(){
      this.$router.push( {name:"chan-push-detail",params:{"projectId" : this.projectId, "save_status" : 'C' }} );
    },
    //  
    fnMod(rowData){
      this.$router.push( {name:"chan-push-detail",params:{"projectId" : this.projectId, "save_status" : 'U', "rowData" : rowData }} );
    },
    //  
    fnDeleteConfirm(rowData){
      this.rowData = rowData;
      eventBus.$on('callbackEventBus', this.fnDelete);
      confirm.fnConfirm("", " PUSH   .  ?", "");
    },
    // 
    fnDelete(){
      var params = {
        "projectId" : this.projectId,
        "loginId"   : tokenSvc.getToken().principal.loginId,
        "appId"     : this.rowData.appId
      }
       
      api.deletePushManage(params).then(response =>{
        var result = response.data;

        if(result.success) {
          confirm.fnAlert("", ".");
          //  
          this.fnSearch();
          // 
        } else {
          confirm.fnAlert("", result.message);
        }
      });
    }
  }
}
</script>