<template>
	<article class="content">
		<section class="userSection width540 mt150">
			<!-- userLogin-form -->
			<div class="userLogin-form">
				<h2 class="text-center">U+   </h2>
				<input type="email" class="form-control" id="inputEmail" placeholder="  "
				 name="userId" v-model="userId" :maxlength="15" v-focus @keyup.enter="ajaxlogin">
				<input type="password" class="form-control mt20" id="inputPassword" placeholder=" "
				 name="loginPwd" v-model="loginPwd" :maxlength="20" @keyup.enter="ajaxlogin"> 
				<!--   -->
				<div class="message_area" v-if="errors.length">
					<p class="text_error">{{ errors[0] }}</p>
				</div>
				<div class="row mt20">
					<div class="checkbox col-md-6">
						<div class="checks">
							<input type="checkbox" id="chkID" class="checkStyle4" value=" " ref="chkSaveId">
							<label for="chkID"> </label>	
						</div>
					</div>
					<div class="forgot col-md-6">
						<a v-bind:href="findUserIdUrl"> </a><span></span>
						<a v-bind:href="findUserPwdUrl"><i class="fas fa-lock-alt mr5"></i>  </a>
					</div>
				</div>	
				<button type="submit" class="btn btn-login mt45" data-toggle="modal" data-target="#smsAcc" @click.prevent="ajaxlogin"></button>
			<!--	<a href="user_join.html" class="btn btn-join mt15"></a> -->
			</div>
			<!-- //userLogin-form -->
		</section>
	</article>
</template>

<script>
import loginApi from '@/modules/login/service/api';
import * as utils from '@/common/utils';

export default {
	data: function() {
		return {
			errors: [],
			userId: '',
			loginPwd: '',
			status: '',
			findUserIdUrl: '/login/findUserId',
			findUserPwdUrl: '/login/findUserPwd'
		};
	},
	created() {
		//   
		this.$store.commit("login/isLogin", false);
		this.$store.commit("login/isErrorPage", false);
	},
	mounted() {
		this.$refs.chkSaveId.checked = true;

		if (localStorage.wtmpltUserId) {  
			this.userId = localStorage.wtmpltUserId;
		}

		let cont = document.querySelector("article.content");
		let contain = document.querySelector("div.container");
	},
	destroyed() {
		let cont = document.querySelector("article.content");
		let contain = document.querySelector("div.container");
	},
	methods: {
		chgChkUserId() {
			if (this.$refs.chkSaveId.checked == true) {
				localStorage.wtmpltUserId = this.userId;
			} else {
				delete localStorage.wtmpltUserId;
			}
		},
		toRegister(){
			this.$router.push({ name: 'register'});
		},
		formCheck: function() {
			this.errors = [];

			if (!this.userId) this.errors.push('  .');
			if (!this.loginPwd) this.errors.push('  .');

			return this.errors.length == 0;
		},
		ajaxlogin: function() {
			var vm = this;
			vm.errmsg = null;

			if (!vm.formCheck()) return false;

			// FormData    Content-Type: multipart/form-data;  .
			//  Object   Content-Type: application/json;charset=UTF-8  .
			var params = {
				//"corpId": this.corpId,
				"userId": this.userId,
				"userPwd": this.loginPwd,
			}

			loginApi.login(params).then(function(response) {
				var rsp = response.data;
				console.log('rsp:', rsp);
				if (rsp.success) {
					vm.chgChkUserId();
					vm.$store.commit("login/isLogin", true);
					vm.$router.push({ path: rsp.data });
				} else {
					vm.$store.commit("login/isLogin", false);
					var msg = null,
					next = null;
					switch (rsp.code) {
						case 'CE_ID_PWD': // ID/PWD 
							vm.errors.push(' . (5    )');
							break;
						case 'SS_NOT_USE': //  
						case 'SS_LOCK': //  
							msg = '     .';
							break;
						case 'SS_PWD_EXPIRE': //  
							msg = '  90 .\n   .';
							next = '/login?expire';
							break;
						case 'SS_INIT_PWD': //    
							msg = '   .\n   .';
							next = function() {
								// vue-router   
								vm.$router.push({ name: 'changepwd', params: { userId: vm.userId } });
							};
							break;
						case 'CE_TO_LOCK': // ID/PWD    
							msg = '5        .\n .';
							break;
						default:
							vm.errmsg = ' .';
					}

					if (msg != null) {
						alert(msg);
						if (next != null) {
							next();
						}
					}
				}
			});
		},
		signUp: function() {
			this.$router.push({name : "signUp"});
		}
	}
};
</script>
