<template>
    <div v-if="pushContsOpen" @click.self="fnClose" class="modalStyle" id="contents" tabindex="-1" role="dialog" aria-hidden="true">
    <div class="modal-dialog">
      <div class="modal-content">
        <div class="modal-body">
          <div class="of_h">
            <div class="float-left" style="width:20%"><h5></h5></div>
            <div class="float-right" style="width:79%">
              <input type="text" class="inputStyle" title=" " v-model="pushTitle" maxlength="45">
            </div>
          </div>
          <div class="of_h mt7">
            <div class="float-left" style="width:20%"><h5> *</h5></div>
            <div class="float-right" style="width:79%">
              <textarea class="textareaStyle height120" :placeholder="contentAreaPlaceholder" v-model="pushContent" maxlength="512"></textarea>
            </div>
            <div v-if="sendData.msgKind == 'A'">
              <p class="txtCaption color5 lc-1 mt7" style="margin-left:21%">   ,    .</p>
              <div class="float-right mt10" style="width:79%">
                <input type="text" class="inputStyle" title=" " placeholder="" v-model="rcvblcNumber">
              </div>
              <p class="txtCaption color5 lc-1 mt7" style="margin-left:21%">   .   (: ) .</p>
            </div>
          </div>
          <div class="of_h mt7">
            <div class="float-left" style="width:20%"><h5>(EXT)</h5></div>
            <div class="float-right" style="width:79%">
              <input type="text" class="inputStyle" title="(EXT) " v-model="adtnInfo" maxlength="45">
            </div>
          </div>
          <div class="text-center mt20">
            <a @click="fnCallbackInputData" class="btnStyle1 backBlack" title=""></a>
            <a @click="fnClose" class="btnStyle1 backWhite" title=""></a>
          </div>

        </div>
      </div>
    </div>
  </div>
</template>

<script>
import confirm from "@/modules/commonUtil/service/confirm.js";

export default {
  name: "pushContentsPopup",
  props: {
    useCh: String,
    pushContsOpen: {
      type: Boolean,
      require: true,
      default: false,
    },
    sendData : {
      type: Object,
      require: true,
    },
    componentsTitle: {
      type: String,
      require: false,
      default: function() {
        return '  ';
      }
    },
  },
  data() {
    return {
      pushTitle: '',
      pushContent : '',
      rcvblcNumber: '',
      adtnInfo: '',
      contentAreaPlaceholder: '    {{ }}  .\n:)    :) {{name}} {{yyyymmdd}}  .',
    }
  },
  watch: {
    pushContsOpen(val){
      if(val) this.fnSetPushInfo();
    }
  },
  methods: {
    // callback
    fnCallbackInputData(){
      if(this.sendData.msgKind == 'A' && !this.rcvblcNumber){
        confirm.fnAlert(this.componentsTitle, '   .');
        return false;
      }
      if(!this.pushContent){
        confirm.fnAlert(this.componentsTitle, ' .');
        return false;
      }
      this.$parent.fnSetPushInfo(this.pushTitle, this.pushContent, this.rcvblcNumber, this.adtnInfo);
      this.fnClose();
    },
    //  Set
    fnSetPushInfo(){
      this.pushTitle = this.sendData.pushTitle;
      this.pushContent = this.sendData.pushContent;
      this.rcvblcNumber = this.sendData.rcvblcNumber;
      this.adtnInfo = this.sendData.adtnInfo;
    },
    // 
    fnClose(){
      this.$emit('update:pushContsOpen', false)
    }
  }
}
</script>
<style lang="scss">
$module: 'modalStyle';
.#{$module} {
  // This is modal bg
  background-color: rgba(0,0,0,.7);
  top: 0; right: 0; bottom: 0; left: 0;
  position: fixed;
  overflow: auto;
  margin: 0;
  z-index: 9999;
}
</style>