<template>
  <div v-if="frndTalkContsOpen" @click.self="fnClose" class="modalStyle" tabindex="-1" role="dialog" aria-hidden="true">
    <div class="modal-dialog">
      <div class="modal-content">
        <div class="modal-body">
          
          <div class="of_h">
            <div class="float-left" style="width:20%"><h5> *</h5></div>
            <div class="float-right" style="width:79%">
              <textarea class="textareaStyle height120" :placeholder="contentAreaPlaceholder" v-model="frndTalkContent" maxlength="2000"></textarea>
            </div>
            <p class="txtCaption color5" style="margin-left:21%">    , 080         .</p>
          </div>
          
          <div class="text-center mt20">
            <a @click="fnCallbackInputData" class="btnStyle2 backBlack" title=""></a>
            <a @click="fnClose" class="btnStyle2 backWhite" data-dismiss="modal" title=""></a>
          </div>

        </div>
      </div>
    </div>
  </div>
</template>

<script>
import confirm from "@/modules/commonUtil/service/confirm.js";

export default {
  name: "frndTalkContentsPopup",
  props: {
    useCh: String,
    frndTalkContsOpen: {
      type: Boolean,
      require: true,
      default: false,
    },
    sendData : {
      type: Object,
      require: true,
    },
    componentsTitle: {
      type: String,
      require: false,
      default: function() {
        return '  ';
      }
    },
  },
  data() {
    return {
      frndTalkContent: '',
      contentAreaPlaceholder: '    {{ }}  .\n:)    :) {{name}} {{yyyymmdd}}  .',
    }
  },
  watch: {
    frndTalkContsOpen(val){
      if(val) this.fnSetFrndTalkInfo();
    }
  },
  methods: {
    fnSetFrndTalkInfo(){
      this.frndTalkContent = this.$gfnCommonUtils.defaultIfEmpty(this.sendData.frndTalkContent, '');
    },
    // callback
    fnCallbackInputData(){
      if(!this.frndTalkContent){
        confirm.fnAlert(this.componentsTitle, ' .');
        return;
      }
      
      this.$parent.fnSetFrndTalkInfo(this.$data);
      this.fnClose();
    },
    // 
    fnClose(){
      this.fnResetData();
      this.$emit('update:frndTalkContsOpen', false)
    },
    fnResetData(){
      Object.assign(this.$data, this.$options.data());
    }
  }
}
</script>
<style lang="scss">
$module: 'modalStyle';
.#{$module} {
  // This is modal bg
  background-color: rgba(0,0,0,.7);
  top: 0; right: 0; bottom: 0; left: 0;
  position: fixed;
  overflow: auto;
  margin: 0;
  z-index: 9999;
}
</style>