<template>
  <div class="modal modalStyle" id="cashPop" tabindex="-1" role="dialog" aria-labelledby="myModalLabel" aria-hidden="true">
    <div class="modal-dialog" style="width: 500px;">
      <div class="modal-content">
        <div class="modal-body">
          <div>
						<h5 class="lc-1"> </h5>
						<hr>
					  <!-- <div class="mt15">
              <h4 class="font-normal inline-block width90 mr10"></h4>
              <select class="inputStyle width291"></select>
            </div> -->
            <div class="mt15">
              <h4 class="font-normal inline-block width90 mr10"></h4>
              <input v-model="amount" class="inputStyle width291">
            </div>
            <div class="mt15">
              *  1,000  .
            </div>
					</div>
          <div class="text-center mt30">
            <button @click="fnConfirm" class="btnStyle4 red mr10"></button>
            <button @click="fnCloseLayer" class="btnStyle3 grey"></button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import cashApi from "@/modules/cash/service/api"
import tokenSvc from '@/common/token-service';

import confirm from "@/modules/commonUtil/service/confirm"
import {eventBus} from "@/modules/commonUtil/service/eventBus";

/* $ npm install @tosspayments/sdk */
import { loadTossPayments } from '@tosspayments/sdk';

export default {
  name: 'prePaidCashPop',
  data() {
    return {
      amount : 0
    }
  },
  mounted() {
    var pgMessage = this.$route.query.pgMessage;

    if(pgMessage) {
      if(pgMessage == "0") {
        pgMessage = " ."
      }

      eventBus.$on('callbackEventBus', this.Refresh);
      confirm.fnConfirm("", pgMessage, "");
    }
  },
  watch: {
    amount(val) {
      const reg1 = /^[0-9]*$/;
      
      val = val.replace(/,/gi, '');
      val = parseInt(val);

      if(isNaN(val)) {
        val = 0;
      }else if(!reg1.test(val)) {
        val = val.toString().slice(0, -1);
      } else {
        val = val.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",");
      }
      
      return this.amount = val;
    }
  },
  methods: {
    fnCloseLayer: function() {
      jQuery("#cashPop").modal("hide");
    },

    fnConfirm: function() {
      var amount = this.amount.replace(/,/gi, '');

      if(amount < 1000) {
        confirm.fnAlert("", " 1,000  .");
      } else if(amount % 1000 != 0) {
        confirm.fnAlert("", " 1,000   .");
      } else if(amount >= 100000000) {
        confirm.fnAlert("", " 100,000,000    .");
      } else {
        var params = {
          "userId"  : tokenSvc.getToken().principal.userId,
          "amount"  : amount
        };

        cashApi.insertWebCashInfo(params).then(response => {
          var result = response.data;
          if(result.success) {
            var data = result.data;

            loadTossPayments(data.clientKey).then(tossPayments => {
              tossPayments.requestPayment('', {
                amount: amount,                                                       //amount ()  number      .
                orderId: data.orderId,                                                //orderId ()  string        ID.    .
                orderName: data.orderName,                                            //orderName ()  string   .   '  1'  .
                customerName: data.customerName,                                      //customerName  string   .       .
                successUrl: window.location.origin + '/projectApi/cash/cardSuccess',  //successUrl ()  string     (Redirect) URL.       (Query Parameter)  .  (origin)  .   'https://www.example.com/success'  .
                failUrl: window.location.origin + '/projectApi/cash/cardFail',        //failUrl ()  string        URL.         .  (origin)  .
                //cardCompany  enum                      .  , BC    BC   .       .
                //cardInstallmentPlan  number              .
                //maxCardInstallmentPlan  number           .   1(),  12   . cardInstallmentPlan    .
                //useCardPoint  boolean                  .             .   true        .   false       .
                //customerEmail  string                .       .
                //customerMobilePhone  string           .        . (-)    .   01012345678  .
                //taxFreeAmount  number                  .
                //useInternationalCardOnly  boolean    true  (Visa, MasterCard, UnionPay)  . ,   . *    .
                //flowMode  'DEFAULT' | 'DIRECT'       'DIRECT',            .    'DIRECT'  cardCompany  .
                //discountCode  string                .        .    API          .   flowMode 'DIRECT'   .
                //appScheme  string                   ISP      .     .   'testapp://'  . *     .
              });
            });
          } else {
            alert(result.message);
          }
        })

        
      }
    },

    Refresh: function() {
      this.$router.push({
        path: "/ac/cash"
      });
    }
  }
}
</script>
