<template>
	<!-- Modify Modal -->
	<!--div class="modal fade modalStyle" id="Modify" tabindex="-1" role="dialog" aria-hidden="true"-->
	<div v-if="layerView" class="layerPopup">
		<div class="modal-dialog">
			<div class="modal-content">
				<div class="modal-body">
					<div>
						<h2> </h2>
						<hr>
						<div class="of_h">
							<h5 class="inline-block" style="width:20%"> ID</h5>
							<input type="text" :value="modifyLoginId" class="inputStyle float-right" style="width:80%" disabled title=" ID ">
						</div>
						<div class="of_h consolMarginTop">
							<h5 class="inline-block"> *</h5>
							<input ref="userName" type="text" :value="modifyUserName" class="inputStyle float-right" style="width:80%" title="  ">
						</div>
						<div class="of_h consolMarginTop">
							<h5 class="inline-block"> *</h5>
							<input ref="hpNumber" type="text" :value="modifyHpNumber" class="inputStyle float-right" style="width:80%" title="  ">
						</div>
						<div class="of_h consolMarginTop">
							<h5 class="inline-block"></h5>
							<select ref="roleCd" :value="modifyRoleCd" class="selectStyle2 float-right inline-block" title=" " style="width:80%">
								<option value="USER">USER</option>
								<option value="OWNER">OWNER</option>
								<option value="ADMIN">ADMIN</option>
							</select>
						</div>
						<p class="color3 consolMarginTop">  SMS       .</p>
					</div>
					<div class="text-center mt40">
						<a @click="fnModifyUser()" class="btnStyle3 black font14 mr5" title=""></a>
						<a @click="fnCloseLayer()" class="btnStyle3 white font14" title=""></a>
					</div>
				</div>
			</div>
		</div>
	<!--/div-->
	</div>
</template>

<script>
import userApi from '../service/userApi'
import confirm from "@/modules/commonUtil/service/confirm";

export default {
	name: 'ModifyLayer',
	props: {
		layerView: {
			type: Boolean,
			require: true,
			default: false
		},
		title: {
			type: String,
			require: false
		},
		modifyUserId: {
			type: String,
			require: true
		},
		modifyUserName: {
			type: String,
			require: false
		},
		modifyHpNumber: {
			type: String,
			require: false
		},
		modifyRoleCd: {
			type: String,
			require: false
		},
		modifyLoginId: {
			type: String,
			require: false
		}
	},
	data() {
		return {
			userName	: '',
			hpNumber	: '',
			roleCd		: ''
		}
	},
	methods: {
		// 
		fnCloseLayer() {
			this.$emit('update:layerView', false);
		},
		// 
		fnModifyUser() {
			console.log('fnModifyUser start');

			// Value assign
			this.fnAssignValue();

			//   
			if(!this.fnInputCheckReq()) return false;

			//  
			if(!this.fnInputCheckChange()) return false;

			var params = {
				"userId"	:this.modifyUserId,
				"userName"	:this.userName,
				"hpNumber"	:this.hpNumber,
				"roleCd"	:this.roleCd
			}

			userApi.modifyUser(params).then(response => {
				var result = response.data;
				if(result.success) {
					confirm.fnAlert("", "  .");
					this.$parent.fnSelectUserList();
				} else {
					confirm.fnAlert("", result.message);
				}
			});

			console.log('fnModifyUser ended');
			this.fnCloseLayer();
		},
		//   
		fnInputCheckReq() {
			if(this.userName == "" || this.userName == null) {
				confirm.fnAlert("", " .");
				return false;
			}

			if(this.hpNumber == "" || this.hpNumber == null) {
				confirm.fnAlert("", " .");
				return false;
			}
			return true;
		},
		// Value assign
		fnAssignValue() {
			console.log("fnAssignValue start");

			this.userName = this.$refs.userName.value;
			this.hpNumber = this.$refs.hpNumber.value;
			this.roleCd = this.$refs.roleCd.value;

			console.log("this.userName : " + this.userName);
			console.log("this.hpNumber : " + this.hpNumber);
			console.log("this.roleCd : " + this.roleCd);

			console.log("fnAssignValue ended");
		},
		//  
		fnInputCheckChange() {
			if(this.userName == this.modifyUserName
				&& this.hpNumber == this.modifyHpNumber
				&& this.roleCd == this.modifyRoleCd) {
				confirm.fnAlert("", "  .");
				return false
			}
			return true;
		}
	}
}
</script>