<template>
	<div>
		<article>
			<div class="contentHeader">
				<h2>RCS </h2>
				<a href="#self" class="btnStyle2 backPink absolute top0 right0" onClick="window.location.reload()" title="RCS  "> <i class="fal fa-book-open"></i></a>
			</div>

			<!--  -->
			<div class="border-line2 pd10">
				<p class="color4">RCS     .<br>RCS     . (  /    /  .)<br>   RCS     .</p>
			</div>

			<div class="row consolMarginTop">
				<div class="col-xs-12">
					<div class="menuBox">
						<div>
							<select name="userConsole_sub040201_1" class="selectStyle2 mr10" style="width:15%" title="RCS  " v-model="status">
								<option value=""></option>
                                <option value=""></option>
                                <option value=""></option>
                                <option value=""></option>
                                <option value=""></option>
                                <option value=""></option>
                                <option value=""></option>
                                <option value="()">()</option>
                                <option value="()">()</option>
                                <option value="()">()</option>
                                <option value="()">()</option>
							</select>
							<select name="userConsole_sub040201_2" class="selectStyle2 " style="width:15%" title="RCS  " v-model="searchTag">
								<option value="temNm"></option>
                                <option value="temId">ID</option>
                                <option value="brandNm"></option>
							</select>
							<input type="text" class="inputStyle vertical-top ml10" style="width:30%" title=" " v-model="inputTag">
							<a href="#self" @click="fnSearch" class="btnStyle1 float-right" title=""></a>	
						</div>					
					</div>
				</div>				
			</div>
			
			<div class="of_h mt20">
				<div class="float-right">
					<a href="#" class="btnStyle2 backBlack" @click.prevent="templateInsert"> </a>
				</div>
			</div>

			<div class="row">
				<div class="col-xs-12">
                    <!-- 15  -->
                    <div class="of_h inline">
                    <div class="float-left"> : <span class="color1"><strong>{{totCnt}}</strong></span>
                        <SelectLayer @fnSelected="fnSelected" classProps="selectStyle2 width120 ml20"></SelectLayer>
                    </div>
                    </div>
					<!-- table -->
					<table class="table_skin1 bt-000 tbl-striped">
						<colgroup>
							<col style="width:5%">
							<col>
							<col style="width:10%">
							<col style="width:10%">
							<col style="width:10%">
							<col style="width:10%">
							<col style="width:10%">
							<col style="width:8%">
							<col style="width:10%">
              <col style="width:9%">
						</colgroup>
						<thead>
							<tr>							
								<th class="text-center lc-1">No.</th>
								<th class="text-center lc-1"> ID</th>
								<th class="text-center lc-1"></th>
								<th class="text-center lc-1"></th>
								<th class="text-center lc-1"></th>
								<th class="text-center lc-1"></th>
								<th class="text-center lc-1"></th>
								<th class="text-center lc-1"></th>
                                <th class="text-center lc-1"></th>
								<th class="text-center lc-1 end"> </th>
							</tr>
						</thead>
						<tbody>
							<tr v-for="(contant, idx) in contants">
                                <td class="text=center">{{totCnt-offset-contant.ROWNUM+1}}</td>
                                <td class="text-center">{{contant.MESSAGEBASE_ID}}</td>
                                <td class="text-center">{{contant.TMPLT_NAME}}</td>
                                <td class="text-center">{{contant.BRAND_NAME}}</td>
                                <td class="text-center">{{contant.BRAND_NAME}}</td>
                                <td class="text-center">{{contant.APPROVAL_STATUS}}</td>
                                <td class="text-center">{{contant.APPROVAL_DT}}</td>
                                <td class="text-center">{{contant.REG_ID}}</td>
                                <td class="text-center">{{contant.REG_DT}}</td>
                                <td class="text-center end"><a href="#" @click="templateCopy(contant)" class="btnStyle6 minWidthAuto" style="width: 65%;"> </a></td>
							</tr>
                            <tr v-if="contants.length == 0">
                            <td class="text-center" colspan="10">  .</td>
                            </tr>
						</tbody>
					</table>
					<!-- //table -->
				</div>			
			</div>

			<!-- pagination -->
            <div id="pageContent">
            <PageLayer @fnClick="fnSearch" :listTotalCnt="totCnt" :selected="listSize" :pageNum="pageNo" ref="updatePaging"></PageLayer>
            </div>
            <!-- //pagination -->
		</article>
	</div>
</template>

<script>
import confirm from "@/modules/commonUtil/service/confirm";
import templateApi from "@/modules/template/service/templateApi.js";
import PageLayer from '@/components/PageLayer.vue';
import SelectLayer from '@/components/SelectLayer.vue';

export default {
  components: {
    PageLayer,
    SelectLayer
  },
  data() {
    return {
      listSize : 10,  // select  value (  )
      pageNo : 1,  //   
      totCnt : 0,  //  
      offset : 0, // 
      status: "",
      searchTag: "temNm",
      inputTag: "",
      contants: []
    }
  },
  mounted() {
    this.pageInfo = {
      "pageCnt"   : [10, 20, 30],   //  
      "selPageCnt": 10,              //  
      "selPage"   : 1,              // 
      "rowNum"    : 0               //
    };

    this.fnPageNoResetSearch();
  },

  methods: {
    // 
    fnSearch() {
		var params = {
            "pageNo" : this.pageNo,
            "listSize" : this.listSize,
            "status" : this.status,
            "searchTag" : this.searchTag,
            "inputTag" : this.inputTag
        };

		templateApi.selectRcsTemplateList(params).then(response => {
			var result = response.data;
        	if(result.success) {
        	  this.contants = result.data;
        	  this.totCnt = result.pageInfo.totCnt;
        	  this.offset = result.pageInfo.offset;
        	}
		});

    },
	// select    
    fnSelected(listSize) {
      this.listSize = Number(listSize);
      this.$refs.updatePaging.fnAllDecrease();
    },
    fnPageNoResetSearch() {
        this.$refs.updatePaging.fnAllDecrease();
    },
	//    
	templateInsert () {
    var params = {};
        
		templateApi.corpBrandCnt(params).then(response => {
            var result = response.data;
            var resultCnt = result.data;
            if(resultCnt > 0) {
                this.$router.push({name : "rcsTemplateMod", params: {status:"INS"}});
            } else {
                confirm.fnAlert("  .", "");
            }
		});
	}
  },
  //  
  templateCopy (contant) {

  }
}
</script>